import React, { useEffect, useState } from "react";
import {
  IonCheckbox,
  IonCol,
  IonDatetime,
  IonIcon,
  IonInput,
  IonLabel,
  IonRow,
  IonSelect,
  IonSelectOption,
} from "@ionic/react";
import "../../../global-styles.scss";
import { calendar, refresh } from "ionicons/icons";
import {
  AuthorizedItems,
  LocationSettings,
} from "../../../orbital-interfaces/authorized-items";
import EventType from "../../../enums/EventType";
import authHelper from "../../../util/authitems-helper";
import WeekdayList from "./WeekdayList";
import { EditorReducerAction, EditorValues } from "../EditEventModalState";
import { RecurrenceType } from "../../../enums/RecurrenceType";
import { formatDateTimeAsMvcString } from "../../../util/time-helper";

interface RecurringEventSectionProps {
  auth: AuthorizedItems;
  editorValues: EditorValues;
  update: React.Dispatch<EditorReducerAction>;
}

// --------------------------------------------------------------------------------------
const RecurringEventSection: React.FC<RecurringEventSectionProps> = ({
  auth,
  editorValues,
  update,
}: RecurringEventSectionProps) => {
  const [locSettings, setLocSettings] = useState(
    null as LocationSettings | null
  );
  const [isVisible, setIsVisible] = useState(false);
  const [isChecked, setIsChecked] = useState(false);

  // --------------------------------------------------------------------------------------
  useEffect(() => {
    if (editorValues.scheduleEventId === -1) {
      return;
    }

    if (editorValues.locationId > 0) {
      setLocSettings(
        authHelper.getLocSettingsForLocationId(auth, editorValues.locationId)
      );
    } else if (editorValues.locationId === 0) {
      setLocSettings(auth.locations[0].settings);
    }
    // eslint-disable-next-line
  }, [editorValues.locationId, editorValues.scheduleEventId]);

  // --------------------------------------------------------------------------------------
  useEffect(() => {
    if (editorValues.scheduleEventId === -1) {
      return;
    }

    setIsVisible(
      editorValues.eventType === EventType.Shift ||
      editorValues.eventType === EventType.Unavailable
    );
  }, [editorValues.eventType, editorValues.scheduleEventId]);

  // --------------------------------------------------------------------------------------
  useEffect(() => {
    if (!isVisible || editorValues.scheduleEventId === -1) {
      return;
    }

    if (editorValues.recurrenceStart) {
      setIsChecked(true);
    } else {
      if (isChecked) {
      } else {
      }
    }
  }, [
    isChecked,
    isVisible,
    editorValues.recurrenceStart,
    editorValues.scheduleEventId,
  ]);

  // --------------------------------------------------------------------------------------
  function onRepeatingChecked() {
    if (!isChecked) {
      update({
        type: "REPEATING-UPDATED",
        repeatWeekDays: undefined,
        repeatInterval: 1,
        recurrenceType: RecurrenceType.Weekly,
        recurrenceEnd: undefined,
        recurrenceStart: formatDateTimeAsMvcString(new Date()),
      });
    } else {
      update({
        type: "REPEATING-UPDATED",
        repeatWeekDays: undefined,
        repeatInterval: undefined,
        recurrenceType: undefined,
        recurrenceEnd: undefined,
        recurrenceStart: undefined,
      });
    }

    setIsChecked(!isChecked);
  }

  // --------------------------------------------------------------------------------------
  function onRecurrenceStartChanged(newValue: string | null | undefined) {
    if (!newValue) {
      update({
        type: "REPEATING-UPDATED",
        repeatWeekDays: editorValues.repeatWeekdays,
        repeatInterval: editorValues.repeatInterval,
        recurrenceType: editorValues.recurrenceType,
        recurrenceEnd: editorValues.recurrenceEnd,
        recurrenceStart: undefined,
      });
    } else {
      update({
        type: "REPEATING-UPDATED",
        repeatWeekDays: editorValues.repeatWeekdays,
        repeatInterval: editorValues.repeatInterval,
        recurrenceType: editorValues.recurrenceType,
        recurrenceEnd: editorValues.recurrenceEnd,
        recurrenceStart: newValue,
      });
    }
  }

  // --------------------------------------------------------------------------------------
  function onRecurrenceEndChanged(newValue: string | null | undefined) {
    if (!newValue) {
      update({
        type: "REPEATING-UPDATED",
        repeatWeekDays: editorValues.repeatWeekdays,
        repeatInterval: editorValues.repeatInterval,
        recurrenceType: editorValues.recurrenceType,
        recurrenceEnd: undefined,
        recurrenceStart: editorValues.recurrenceStart,
      });
    } else {
      update({
        type: "REPEATING-UPDATED",
        repeatWeekDays: editorValues.repeatWeekdays,
        repeatInterval: editorValues.repeatInterval,
        recurrenceType: editorValues.recurrenceType,
        recurrenceEnd: newValue,
        recurrenceStart: editorValues.recurrenceStart,
      });
    }
  }

  // --------------------------------------------------------------------------------------
  function onRecurrenceTypeChanged(newValue: string | null | undefined) {
    if (!newValue) {
      update({
        type: "REPEATING-UPDATED",
        repeatWeekDays: editorValues.repeatWeekdays,
        repeatInterval: editorValues.repeatInterval,
        recurrenceType: undefined,
        recurrenceEnd: editorValues.recurrenceEnd,
        recurrenceStart: editorValues.recurrenceStart,
      });
    } else {
      update({
        type: "REPEATING-UPDATED",
        repeatWeekDays: editorValues.repeatWeekdays,
        repeatInterval: editorValues.repeatInterval,
        recurrenceType: parseInt(newValue),
        recurrenceEnd: editorValues.recurrenceEnd,
        recurrenceStart: editorValues.recurrenceStart,
      });
    }
  }

  // --------------------------------------------------------------------------------------
  function onRepeatIntervalChanged(newValue: string | null | undefined) {
    if (!newValue) {
      update({
        type: "REPEATING-UPDATED",
        repeatWeekDays: editorValues.repeatWeekdays,
        repeatInterval: undefined,
        recurrenceType: editorValues.recurrenceType,
        recurrenceEnd: editorValues.recurrenceEnd,
        recurrenceStart: editorValues.recurrenceStart,
      });
    } else {
      update({
        type: "REPEATING-UPDATED",
        repeatWeekDays: editorValues.repeatWeekdays,
        repeatInterval: parseInt(newValue),
        recurrenceType: editorValues.recurrenceType,
        recurrenceEnd: editorValues.recurrenceEnd,
        recurrenceStart: editorValues.recurrenceStart,
      });
    }
  }

  if (!isVisible) {
    return <span />;
  }

  return (
    <>
      <IonRow>
        <IonCol size="3">
          <IonLabel className="label">(R)epeats</IonLabel>
        </IonCol>

        <IonCol size="2">
          <IonCheckbox
            checked={isChecked}
            onClick={onRepeatingChecked}
            disabled={editorValues.isReadonly}
          />
        </IonCol>
      </IonRow>

      {isChecked && (
        <>
          <IonRow className="edit-row">
            <IonCol size="1">
              <IonIcon icon={calendar} mode="md" />
            </IonCol>
            <IonCol size="5">
              <IonDatetime
                mode="ios"
                value={editorValues.recurrenceStart}
                className="select"
                displayFormat="M/D/YY, DDD"
                pickerFormat="MMM DD YYYY"
                disabled={editorValues.isReadonly}
                min="2005-01-01"
                max="2030-01-01"
                onIonChange={(e) => {
                  onRecurrenceStartChanged(e.detail.value);
                }}
              />
            </IonCol>
            <IonCol size="1">
              <IonIcon
                icon={calendar}
                mode="md"
                style={{ textAlign: "right" }}
              />
            </IonCol>
            <IonCol size="5">
              <IonDatetime
                mode="ios"
                value={editorValues.recurrenceEnd}
                className="select"
                displayFormat="M/D/YY, DDD"
                pickerFormat="MMM DD YYYY"
                disabled={editorValues.isReadonly}
                min="2005-01-01"
                max="2030-01-01"
                onIonChange={(e) => {
                  onRecurrenceEndChanged(e.detail.value);
                }}
              />
            </IonCol>
          </IonRow>
          <IonRow className="edit-row">
            <IonCol size="1">
              <IonIcon icon={refresh} mode="md" />
            </IonCol>
            <IonCol size="5">
              <IonSelect
                mode="ios"
                interface="action-sheet"
                className="select"
                value={editorValues.recurrenceType}
                disabled={editorValues.isReadonly}
                onIonChange={(e) => {
                  onRecurrenceTypeChanged(e.detail.value);
                }}
              >
                <IonSelectOption value={RecurrenceType.Weekly}>
                  Weekly
                </IonSelectOption>
                <IonSelectOption value={RecurrenceType.MonthlyByDayOfMonth}>
                  Monthly (Day of Month)
                </IonSelectOption>
                <IonSelectOption value={RecurrenceType.MonthlyByDayOfWeek}>
                  Monthly (Day of Week)
                </IonSelectOption>
              </IonSelect>
            </IonCol>
            <IonCol size="1">#</IonCol>
            <IonCol size="5">
              <IonInput
                className="input-textbox"
                disabled={editorValues.isReadonly}
                value={
                  editorValues.repeatInterval
                    ? editorValues.repeatInterval.toString()
                    : undefined
                }
                onIonChange={(e) => onRepeatIntervalChanged(e.detail.value)}
              />
            </IonCol>
          </IonRow>
          <IonRow>
            <IonCol size="12">
              <WeekdayList
                locSettings={locSettings}
                editorValues={editorValues}
                update={update}
              />
            </IonCol>
          </IonRow>
        </>
      )}
    </>
  );
};

export default RecurringEventSection;
