import React, { useState, useEffect, useContext } from "react";
import "../../global-styles.scss";
import { ApplicationUserContext } from "../../context/ApplicationUserContext";
import useDialogBox from "../../hooks/useDialogBox";
import {
  IonContent,
  IonModal,
  IonSpinner,
  useIonViewWillEnter,
  useIonViewWillLeave,
} from "@ionic/react";
import { getDurationFromTimeSpan } from "../../util/time-helper";
import { EventInformation, HoursList } from "../../orbital-interfaces/TEC";
import EditEventModal from "../EditEvent/EditEventModal";
import { RefresherEventDetail } from "@ionic/core";
import useIonicRefresher from "../../hooks/useIonicRefresher";
import { AuthorizedItems } from "../../orbital-interfaces/authorized-items";
import EventCardSkeleton from "../../components/SchedulePageComponents/EventCardSkeleton";
import { EventEditAuthorization } from "../EditEvent/EditEventAuthorization";
import {
  getEditorValuesFromEventInformation,
  INITIAL_EDITOR_VAUES,
} from "../EditEvent/EditEventModalState";
import { AuthorizedItemsContext } from "../../context/AuthorizedItemsContext";
import { useInterval } from "../../hooks/useInterval";
import { getConstants } from "../../api/environment";
import { EventFilter } from "../../orbital-interfaces/EventFilter";
import EventGroup from "../../enums/EventGroup";
import ScheduleEventsAPI from "../../api/ScheduleEventsAPI";
import authHelper from "../../util/authitems-helper";
import { addHours } from "date-fns";
import { DailyNotes } from "../../orbital-interfaces/DailyNotes";
import EventList from "../SchedulePageComponents/EventList";
import useSimpleToast from "../../hooks/useSimpleToast";

interface ClockTodayTabProps {
  refreshToggle: boolean;
  onUpdate: () => void;
}

// ------------------------------------------------------------------------------------
function createTodayFilter(auth: AuthorizedItems): EventFilter {
  const positionIds = authHelper.getPositionIdsForEmployee(auth);

  const locationIds = [] as number[];
  for (let i = 0; i < positionIds.length; i++) {
    const locId = authHelper.getLocationIdFromPositionid(auth, positionIds[i]);
    if (!locationIds.includes(locId)) {
      locationIds.push(locId);
    }
  }

  const locSettings = authHelper.getLocSettingsForLocationId(
    auth,
    locationIds[0]
  );
  const dayStartMinutes =
    !locSettings.DayStartTime || locSettings.DayStartTime.indexOf("T") < 0
      ? 0
      : getDurationFromTimeSpan(locSettings.DayStartTime.split("T")[1]);
  const start = new Date();
  start.setHours(0, dayStartMinutes, 0, 0);

  const end = addHours(start, 24);

  const filter: EventFilter = {
    settingsLocId: locationIds[0],
    positionIds,
    start,
    end,
    filterByEmployee: true,
    dnLocIds: locationIds,
    filterByPosition: false,
    tagId: undefined,
    eventTypeId: EventGroup.All,
    eventStatusId: EventGroup.All,
  };

  return filter;
}

// ------------------------------------------------------------------------------------
//
// ------------------------------------------------------------------------------------
const ClockTodayTab: React.FC<ClockTodayTabProps> = ({
  refreshToggle,
  onUpdate,
}: ClockTodayTabProps) => {
  const doRefresh = (e: CustomEvent<RefresherEventDetail>) => {
    getData(e);
  };

  const [scheduleEvents, setScheduleEvents] = useState(
    [] as EventInformation[]
  );
  const [dailyNotes, setDailyNotes] = useState([] as DailyNotes[]);
  const [hoursPerDay, setHoursPerDay] = useState([] as HoursList[]);
  const toast = useSimpleToast();
  const refresher = useIonicRefresher(doRefresh);
  const { user } = React.useContext(ApplicationUserContext);
  const dlg = useDialogBox();
  const [showEventModal, setShowEventModal] = useState(false);
  const [editEventId, setEditEventId] = useState(null as number | null);
  const [eventEditAuth, setEventEditAuth] = useState(
    null as EventEditAuthorization | null
  );
  const [initialEditEvent, setInitialEditEvent] = useState(
    INITIAL_EDITOR_VAUES
  );
  const [isLoading, setIsLoading] = useState(false);
  const [isShowing, setIsShowing] = useState(false);
  const { auth } = useContext(AuthorizedItemsContext);

  // ------------------------------------------------------------------------------------
  useIonViewWillEnter(() => {
    setIsShowing(true);
  });

  // ------------------------------------------------------------------------------------
  useIonViewWillLeave(() => {
    setIsShowing(false);
  });

  // ------------------------------------------------------------------------------------
  const forceRefresh = (toastMessage: string) => {
    if (toastMessage && toastMessage.length > 0) {
      toast.showToast(toastMessage);
    }

    getData();
  };

  // ------------------------------------------------------------------------------------
  useInterval(
    () => {
      getData();
    },
    isShowing && !showEventModal,
    getConstants(user).refreshScheduleTimer
  );

  // ------------------------------------------------------------------------------------
  const onEventClick = (
    ev: EventInformation,
    newEventEditAuth: EventEditAuthorization | null
  ) => {
    setEditEventId(ev.ScheduleEventId);
    setEventEditAuth(newEventEditAuth);
    setInitialEditEvent(getEditorValuesFromEventInformation(auth, ev));
    setShowEventModal(true);
  };

  // ------------------------------------------------------------------------------------
  function getData(e?: CustomEvent<RefresherEventDetail>) {
    try {
      const apiFilter = createTodayFilter(auth);
      ScheduleEventsAPI.getScheduleEventsByFilter(user, apiFilter).then(
        (res) => {
          setIsLoading(false);

          if (e) {
            e.detail.complete();
          }

          onUpdate();
          setScheduleEvents(res.events);
          setDailyNotes(res.dailyNotes);
          setHoursPerDay(res.hoursPerDay);
        }
      );
    } catch (e) {
      console.log("Error in ClockTodayTab.getData: " + e.message);
    }
  }

  // ------------------------------------------------------------------------------------
  useEffect(() => {
    // eslint-disable-next-line
    if (user && user.isLoggedin) {
      setIsLoading(true);
      getData(undefined);
    }

    // eslint-disable-next-line
  }, [refreshToggle]);

  // ------------------------------------------------------------------------------------
  const onEventUpdated = () => {
    getData(undefined);
    setShowEventModal(false);
  };

  return (
    <>
      <IonContent>
        {refresher.ionicRefresherJsx}
        {isLoading &&
          ((dailyNotes && dailyNotes.length > 0) ||
            (scheduleEvents && scheduleEvents.length > 0)) && (
            <IonSpinner className="center" name="dots" />
          )}

        {isLoading && (dailyNotes.length === 0 || scheduleEvents.length === 0) && (
          <>
            <EventCardSkeleton threeRows={false} />
            <EventCardSkeleton threeRows={false} />
          </>
        )}

        {!isLoading &&
          (scheduleEvents?.length > 0 || dailyNotes?.length > 0) && (
            <EventList
              events={scheduleEvents}
              listType={"today"}
              notes={dailyNotes}
              hoursPerDay={hoursPerDay}
              auth={auth}
              onEdit={onEventClick}
              onForceRefresh={forceRefresh}
              weekStart={new Date()}
            />
          )}
      </IonContent>

      {toast.simpleToastJsx}

      <IonModal
        isOpen={showEventModal}
        onDidDismiss={() => setShowEventModal(false)}
      >
        <EditEventModal
          isOpen={showEventModal}
          auth={auth}
          onCancelModal={() => setShowEventModal(false)}
          onDismissModal={() => onEventUpdated()}
          scheduleEventId={editEventId}
          eventEditAuth={eventEditAuth!}
          initialEvent={initialEditEvent}
        />
      </IonModal>

      {dlg.alertBoxJsx}
    </>
  );
};

export default ClockTodayTab;
