/* eslint-disable */
import React, { useState, useEffect } from "react";
import db from "../data/dbStore";
import PushController from "../util/push-notifications-controller";
import env from "../api/environment";

export interface ApplicationUser {
  isLoggedin: boolean;
  access_token?: string;
  username: string;
  darkMode: boolean;
  loading: boolean;
  environment: string;
  isPushEnabled: boolean;
}

export interface IApplicationUserContext {
  user: ApplicationUser;
  logout: () => Promise<void>;
  userLoggedIn: (
    username: string,
    access_token: string,
    environment: string
  ) => Promise<ApplicationUser>;
  pushEnabled: (enabled: boolean) => void;
}

const INITIAL_APPLICATION_USER: ApplicationUser = {
  isLoggedin: false,
  access_token: undefined,
  username: "",
  darkMode: false,
  loading: true,
  environment: "prod",
  isPushEnabled: false,
};

const INITIAL_APPLICATION_USER_CONTEXT = {
  user: INITIAL_APPLICATION_USER,
  logout: async () => {},
  userLoggedIn: (u: string, at: string, e: string) => {
    return new Promise<ApplicationUser>(() => {
      return INITIAL_APPLICATION_USER;
    });
  },
  pushEnabled: (e: boolean) => {},
};

export const ApplicationUserContext = React.createContext<
  IApplicationUserContext
>(INITIAL_APPLICATION_USER_CONTEXT);

export const ApplicationUserContextProvider = (props: any) => {
  const [userState, internalSetUserState] = useState({
    ...INITIAL_APPLICATION_USER,
  });

  // ----------------------------------------------------------------------------------------------
  useEffect(() => {
    let mounted = true;

    Promise.all([db.getUserState()]).then((response) => {
      if (mounted) {
        const newState = response[0];
        if (newState) {
          internalSetUserState({ ...newState, loading: false });
        } else {
          internalSetUserState({ ...userState, loading: false });
        }
      }
    });
    return () => {
      mounted = false;
    };

    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (
      userState &&
      !userState.isPushEnabled &&
      userState.access_token &&
      userState.access_token.length > 0 &&
      env.settings.platform !== "other"
    ) {
      PushController.register(userState, pushEnabled, () => {});
    }

    // eslint-disable-next-line
  }, [userState.isPushEnabled, userState.access_token]);

  // ----------------------------------------------------------------------------------------------
  const pushEnabled = (enabled: boolean) => {
    internalSetUserState({ ...userState, isPushEnabled: enabled });
  };

  // ----------------------------------------------------------------------------------------------
  const userLoggedIn = async (
    username: string,
    access_token: string,
    environment: string
  ): Promise<ApplicationUser> => {
    const currentState = await db.getUserState();
    const newState = {
      username,
      access_token,
      environment,
      isLoggedin: true,
      darkMode: currentState
        ? currentState.darkMode
        : INITIAL_APPLICATION_USER.darkMode,
      loading: false,
      isPushEnabled: false,
    };

    await db.putUserState(newState);
    internalSetUserState(newState);
    return newState;
  };

  // ----------------------------------------------------------------------------------------------
  const logout = async () => {
    const newState = {
      ...userState,
      isLoggedin: false,
      access_token: undefined,
      latitude: undefined,
      longitude: undefined,
      isPushEnabled: false,
      requestedGeoPermission: false,
      refusedGeoPermission: false,
    };
    internalSetUserState(newState);
    await db.putUserState(newState);
  };

  return (
    <ApplicationUserContext.Provider
      value={{
        user: userState,
        userLoggedIn,
        logout,
        pushEnabled,
      }}
    >
      {props.children}
    </ApplicationUserContext.Provider>
  );
};
