import React, { useContext, useState } from "react";
import { IonRow, IonCol, IonModal } from "@ionic/react";
import { format, parseISO } from "date-fns";
import { PayrollPeriodDateItem } from "../../api/PayrollPeriodAPI";
import PayrollOrbitalTimeItem from "./PayrollOrbitalTimeItem";
import { AuthorizedItemsContext } from "../../context/AuthorizedItemsContext";
import authHelper from "../../util/authitems-helper";
import { ColumnEnum } from "./PayrollDetailList";
import PayrollDetailModal from "./PayrollDetailModal";
import Skeleton from "react-loading-skeleton";

interface PayrollDetailListProps {
  payrollRow: PayrollPeriodDateItem | null;
  colsToDisplay: ColumnEnum;
  colSize: string;
}

function formatDateString(d: string) {
  // return format(parseISO(d), "EEE d").toUpperCase();
  return format(parseISO(d), "EEE d");
}

// --------------------------------------------------------------------------------------
const PayrollDetailRow: React.FC<PayrollDetailListProps> = ({
  payrollRow,
  colsToDisplay,
  colSize,
}: PayrollDetailListProps) => {
  const { auth } = useContext(AuthorizedItemsContext);
  const locSettings = authHelper.getLocSettingsForLocationId(
    auth,
    auth.locations[0].id
  );
  const [showDetailModal, setShowDetailModal] = useState(false);

  if (payrollRow === null) {
    return (
      <IonRow>
        <IonCol size="3">
          <Skeleton width={50} />
        </IonCol>
        <IonCol size="3">
          <Skeleton width={50} />
        </IonCol>
        <IonCol size="3">
          <Skeleton width={50} />
        </IonCol>
        <IonCol size="3">
          <Skeleton width={50} />
        </IonCol>
      </IonRow>
    );
  }

  return (
    <>
      <IonRow>
        <IonCol size={colSize}>
          <button
            className="payperiod-list-buttons"
            onClick={() => setShowDetailModal(true)}
          >
            {formatDateString(payrollRow!.date)}
          </button>
        </IonCol>

        <IonCol
          size={colSize}
          color="secondary"
          hidden={(colsToDisplay & ColumnEnum.Regular) !== ColumnEnum.Regular}
        >
          <PayrollOrbitalTimeItem
            time={payrollRow?.regular}
            locSettings={locSettings}
            fontColor="black"
          />
        </IonCol>

        <IonCol
          size={colSize}
          hidden={
            (colsToDisplay & ColumnEnum.DailyOvertime) !==
            ColumnEnum.DailyOvertime
          }
        >
          <PayrollOrbitalTimeItem
            time={payrollRow?.dailyOvertime}
            locSettings={locSettings}
            fontColor="red"
          />
        </IonCol>

        <IonCol
          size={colSize}
          hidden={
            (colsToDisplay & ColumnEnum.WeeklyOvertime) !==
            ColumnEnum.WeeklyOvertime
          }
        >
          <PayrollOrbitalTimeItem
            time={payrollRow?.weeklyOvertime}
            locSettings={locSettings}
            fontColor="red"
          />
        </IonCol>

        <IonCol
          size={colSize}
          hidden={(colsToDisplay & ColumnEnum.Special) !== ColumnEnum.Special}
        >
          <PayrollOrbitalTimeItem
            time={payrollRow?.special}
            locSettings={locSettings}
            fontColor="black"
          />
        </IonCol>

        <IonCol
          size={colSize}
          hidden={(colsToDisplay & ColumnEnum.Absent) !== ColumnEnum.Absent}
        >
          <PayrollOrbitalTimeItem
            time={payrollRow?.absent}
            locSettings={locSettings}
            fontColor="black"
          />
        </IonCol>
      </IonRow>

      <IonModal
        cssClass="mini-modal detail-modal"
        isOpen={showDetailModal}
        onDidDismiss={() => setShowDetailModal(false)}
      >
        <PayrollDetailModal
          onClose={() => setShowDetailModal(false)}
          locSettings={locSettings}
          data={payrollRow!}
        />
      </IonModal>
    </>
  );
};

export default PayrollDetailRow;
