import React, {useEffect, useState} from "react";
import {IonCol, IonInput, IonLabel, IonRow, IonSelect, IonSelectOption,} from "@ionic/react";
import "../../../global-styles.scss";
import {
  AdminAuthorization,
  AuthorizedItems,
  LocationSettings,
  Role,
  TimeFormat,
} from "../../../orbital-interfaces/authorized-items";
import EventType, {getEventTypeIdName} from "../../../enums/EventType";
import EventStatus, {eventStatusIdList, getEventStatusIdName} from "../../../enums/EventStatus";
import {EditorReducerAction, EditorValues} from "../EditEventModalState";
import {getMinutesFromTimeString, getTimeStringFromMinutes,} from "../../../util/time-helper";
import authHelper from "../../../util/authitems-helper";
import {IdName} from "../../../orbital-interfaces/IdName";

interface EventTypeStatusEditorProps {
  auth: AuthorizedItems;
  editorValues: EditorValues;
  update: React.Dispatch<EditorReducerAction>;
}

// --------------------------------------------------------------------------------------
function getSelectedEventTypeId(ev: EditorValues) {
  if (ev.eventType !== EventType.Absent) {
    return ev.eventType;
  }

  return ev.absentId;
}

// --------------------------------------------------------------------------------------
function showClockEventType(
  auth: AuthorizedItems,
  ev: EditorValues,
  adminAuth: AdminAuthorization)
{
  if (auth.role === Role.BusinessAdmin || auth.role === Role.LocationAdmin) {
    return true;
  }

  if (auth.role === Role.Employee) {
    return false;
  }

  if (ev.employee.id === auth.employeeId) {
    return authHelper.adminCan(adminAuth, AdminAuthorization.CanEditOwnClockEvents);
  }

  return authHelper.adminCan(adminAuth, AdminAuthorization.CanEditEmployeeClockEvents);
}

// --------------------------------------------------------------------------------------
function getPayrollEventIdNames(auth: AuthorizedItems, posId: number) {
  return auth.payrollEvents
    .filter(
      (pe) =>
        pe.positionIds.includes(posId) &&
        !pe.name.startsWith("--")
    )
    .map((pe) => {
      return { id: pe.id, name: pe.name };
    }).sort((a,b) => (a.name > b.name)
      ? 1
      : (b.name > a.name) ? -1 : 0);
}

// --------------------------------------------------------------------------------------
function getEventTypes(auth: AuthorizedItems, ev: EditorValues): IdName[]  {
  if (!authHelper.isAuthItemsValid(auth)) {
    return [] as IdName[];
  }

  let payrollEvents: IdName[] = [];
  const eventTypes: EventType[] = [];
  const locSettings = ev.position.location.settings;
  const dept = authHelper.getDepartmentFromPositionId(auth, ev.position.position.id);
  if (dept === null) {
    return [] as IdName[];
  }

  const adminAuth = authHelper.getAdminAuthorizationForDepartment(auth, dept!.id);

  if (showClockEventType(auth, ev, adminAuth)) {
    eventTypes.push(EventType.Clock);
  }

  eventTypes.push(EventType.Shift);

  if (auth.role === Role.BusinessAdmin || auth.role === Role.LocationAdmin ||
      (auth.role === Role.Admin &&
       dept !== undefined &&
       authHelper.isAdminAuthorizedFor(
         auth,
         ev.locationId,
         dept.id,
         AdminAuthorization.CanEditSchedules))) {

    eventTypes.push(EventType.Cover);

    if (locSettings.ShowVacationEventType) {
      eventTypes.push(EventType.Vacation);
    }

    if (locSettings.ShowUnavailableEventType) {
      eventTypes.push(EventType.Unavailable);
    }

    if (locSettings.ShowAbsentEventType) {
      payrollEvents = getPayrollEventIdNames(auth, ev.position.position.id);
    }
  } else if (auth.role === Role.Admin && dept !== undefined) {
    if (locSettings.ShowVacationEventType) {
      eventTypes.push(EventType.Vacation);
    }

    if (locSettings.ShowUnavailableEventType) {
      eventTypes.push(EventType.Unavailable);
    }

    if (locSettings.ShowAbsentEventType) {
      payrollEvents = getPayrollEventIdNames(auth, ev.position.position.id);
    }
  } else {
    if (ev.scheduleEventId > 0) {
      return [getEventTypeIdName(ev.eventType)];
    }

    if (locSettings.ShowVacationEventType) {
      eventTypes.push(EventType.Vacation);
    }

    if (locSettings.ShowUnavailableEventType) {
      eventTypes.push(EventType.Unavailable);
    }

    if (locSettings.ShowAbsentEventType) {
      payrollEvents = getPayrollEventIdNames(auth, ev.position.position.id);
    }
  }

  const idNames = eventTypes.map(et => {return getEventTypeIdName(et)}) as IdName[];
  return idNames.concat(payrollEvents);
}

// --------------------------------------------------------------------------------------
function getEventStatuses(auth: AuthorizedItems, ev: EditorValues): IdName[] {
  if (!authHelper.isAuthItemsValid(auth)) {
    return [] as IdName[];
  }

  if (auth.role === Role.Employee) {
    if (ev.scheduleEventId === 0) {
      return [getEventStatusIdName(EventStatus.Request)]
    } else {
      return [getEventStatusIdName(ev.eventStatus)];
    }
  }

  if (auth.role === Role.LocationAdmin || auth.role === Role.BusinessAdmin) {
    return eventStatusIdList;
  }

  const dept = authHelper.getDepartmentFromPositionId(auth, ev.position.position.id);
  if (dept === null) {
    return [] as IdName[];
  }

  if (authHelper.isAdminAuthorizedFor(
    auth,
    ev.locationId,
    dept!.id,
    AdminAuthorization.CanEditSchedules)) {
    return eventStatusIdList;
  }

  return [getEventStatusIdName(EventStatus.Request)];
}

// --------------------------------------------------------------------------------------
//
// --------------------------------------------------------------------------------------
const EventTypeStatusEditor: React.FC<EventTypeStatusEditorProps> = ({
  auth,
  editorValues,
  update,
}: EventTypeStatusEditorProps) => {
  const [absentHoursPlaceholderText, setAbsentHoursPlaceholderText] = useState(
    "Ex: 00:00"
  );
  const [locSettings, setLocSettings] = useState(
    null as LocationSettings | null
  );
  const [absentTimeString, setAbsentTimeString] = useState(
    null as string | null
  );

  // ------------------------------------------------------------------------------------
  useEffect(() => {
    if (editorValues.scheduleEventId === -1) {
      return;
    }
    // If this is an employee and we're creating an event
    if (
      auth.role === Role.Employee &&
      editorValues.scheduleEventId <= 0 &&
      editorValues.eventStatus !== EventStatus.Request
    ) {
      update({
        type: "TYPESTATUS-UPDATED",
        eventType: editorValues.eventType,
        eventStatus: EventStatus.Request,
        absentId: editorValues.absentId,
        absentTimeMinutes: editorValues.absentTimeMinutes,
      });
    }


    // eslint-disable-next-line
  }, [
    auth,
    editorValues.eventType,
    editorValues.eventStatus,
    editorValues.position,
    editorValues.absentId,
    editorValues.scheduleEventId,
  ]);

  // ------------------------------------------------------------------------------------
  useEffect(() => {
    if (editorValues.isFromEventInfo) {
      return; // won't have accurrate data for absents yet
    }

    const eventTypes = getEventTypes(auth, editorValues);
    if (eventTypes.length === 0) {
      return;
    }

    const curEventType = eventTypes.find(et => et.id === getSelectedEventTypeId(editorValues));
    if (curEventType === undefined) {
      update({
        type: "TYPESTATUS-UPDATED",
        eventType: eventTypes[0].id > 100 ? EventType.Absent : eventTypes[0].id,
        eventStatus: editorValues.eventStatus,
        absentId: eventTypes[0].id > 100 ? eventTypes[0].id : undefined,
        absentTimeMinutes: editorValues.absentTimeMinutes
      });
    }

    // eslint-disable-next-line
  }, [editorValues.employee, editorValues.isFromEventInfo]);

  // ------------------------------------------------------------------------------------
  useEffect(() => {
    const eventStatuses = getEventStatuses(auth, editorValues);
    const curEventStatus = eventStatuses.find(es => es.id === editorValues.eventStatus);
    if (curEventStatus === undefined) {
      update({
        type: "TYPESTATUS-UPDATED",
        eventType: editorValues.eventType,
        eventStatus: eventStatuses[0].id,
        absentId: editorValues.absentId,
        absentTimeMinutes: editorValues.absentTimeMinutes
      });
    }

    // eslint-disable-next-line
  }, [editorValues.position]);


  // ------------------------------------------------------------------------------------
  useEffect(() => {
    if (editorValues.scheduleEventId === -1) {
      return;
    }

    if (locSettings) {
      setAbsentTimeString(
        editorValues.absentTimeMinutes === null
          ? null
          : getTimeStringFromMinutes(
              locSettings,
              editorValues.absentTimeMinutes
            )
      );
    }
  }, [
    locSettings,
    editorValues.absentTimeMinutes,
    editorValues.scheduleEventId,
  ]);

  // ------------------------------------------------------------------------------------
  useEffect(() => {
    if (editorValues.scheduleEventId === -1) {
      return;
    }

    if (locSettings && editorValues.locationId === locSettings.LocationId) {
      return;
    }

    if (!auth || !auth.locations || auth.locations.length === 0) {
      return;
    }

    const newLocSettings = authHelper.getLocSettingsForLocationId(
      auth,
      editorValues.locationId
    );
    setLocSettings(newLocSettings);
    setAbsentHoursPlaceholderText(
      newLocSettings.TimeFormat === TimeFormat.HoursMinutes
        ? "Ex: 00:00"
        : "Ex: 0.0"
    );
    // eslint-disable-next-line
  }, [editorValues.locationId, editorValues.scheduleEventId]);

  // ------------------------------------------------------------------------------------
  const onEventTypeChange = (newEventType: EventType) => {
    if (newEventType === editorValues.eventType) {
      return;
    }

    const newAbsentMinutes =
      newEventType !== EventType.Absent && newEventType < 100
        ? null
        : editorValues.absentTimeMinutes;

    update({
      type: "TYPESTATUS-UPDATED",
      eventType: newEventType >= 100 ? EventType.Absent : newEventType,
      eventStatus: editorValues.eventStatus,
      absentId: newEventType >= 100 ? newEventType : undefined,
      absentTimeMinutes: newAbsentMinutes,
    });
  };

  // ------------------------------------------------------------------------------------
  const onEventStatusChange = (newEventStatus: EventStatus) => {
    if (newEventStatus === editorValues.eventStatus) {
      return;
    }

    update({
      type: "TYPESTATUS-UPDATED",
      eventType: editorValues.eventType,
      eventStatus: newEventStatus,
      absentId: editorValues.absentId,
      absentTimeMinutes: editorValues.absentTimeMinutes,
    });
  };

  // ------------------------------------------------------------------------------------
  const onAbsentTimeChange = (input?: string | null) => {
    if (input === undefined) {
      input = null;
    }

    if (input == null) {
      update({
        type: "TYPESTATUS-UPDATED",
        eventType: editorValues.eventType,
        eventStatus: editorValues.eventStatus,
        absentId: editorValues.absentId,
        absentTimeMinutes: null,
      });

      return;
    }

    const min = getMinutesFromTimeString(locSettings!, input!);
    if (min === editorValues.absentTimeMinutes) {
      return;
    }

    update({
      type: "TYPESTATUS-UPDATED",
      eventType: editorValues.eventType,
      eventStatus: editorValues.eventStatus,
      absentId: editorValues.absentId,
      absentTimeMinutes: min,
    });
  };

  return (
    <>
      <IonRow className="edit-row">
        <IonCol size="3">
          <IonLabel className="label">Type</IonLabel>
        </IonCol>
        <IonCol size="9">
          <IonSelect
            mode="ios"
            className="select"
            interface="action-sheet"
            disabled={editorValues.isReadonly}
            value={getSelectedEventTypeId(editorValues)}
            onIonChange={(e) => {
              onEventTypeChange(e.detail.value);
            }}
          >
            {getEventTypes(auth, editorValues).map((et) => (
              <IonSelectOption key={et.id} value={et.id}>
                {et.name}
              </IonSelectOption>
            ))}
          </IonSelect>
        </IonCol>
      </IonRow>

      <IonRow className="edit-row">
        <IonCol size="3">
          <IonLabel className="label">Status</IonLabel>
        </IonCol>
        <IonCol size="9">
          <IonSelect
            mode="ios"
            className="select"
            interface="action-sheet"
            disabled={
              editorValues.isReadonly ||
              (auth.role === Role.Employee && editorValues.scheduleEventId <= 0)
            }
            value={editorValues.eventStatus}
            onIonChange={(e) => {
              onEventStatusChange(e.detail.value);
            }}
          >
            {getEventStatuses(auth, editorValues).map((es) => (
                <IonSelectOption key={es.id} value={es.id}>
                  {es.name}
                </IonSelectOption>
              ))}
          </IonSelect>
        </IonCol>
      </IonRow>

      <IonRow
        className={editorValues.eventType === EventType.Absent ? "visible edit-row" : "invisible"}
      >
        <IonCol size="3">
          <IonLabel className="label">Hours</IonLabel>
        </IonCol>
        <IonCol size="9">
          <IonInput
            className="input-textbox"
            placeholder={absentHoursPlaceholderText}
            disabled={editorValues.isReadonly}
            value={absentTimeString}
            onIonChange={(e) => {
              if (e.detail.value) {
                setAbsentTimeString(e.detail.value);
              }
            }}
            onIonBlur={() => {
              onAbsentTimeChange(absentTimeString);
            }}
          />
        </IonCol>
      </IonRow>
    </>
  );
};

export default EventTypeStatusEditor;
