import React, { useState} from "react";
import {
  IonContent,
  IonPage,
  IonRow,
  IonCol,
  IonButton,
  IonList,
  IonText,
  IonLoading,
  useIonViewWillEnter,
} from "@ionic/react";
// import "./Login.scss";
import "../global-styles.scss";
import UserAPI from "../api/UserAPI";
import { RouteComponentProps } from "react-router";
import useDialogBox from "../hooks/useDialogBox";
import { ApplicationUserContext } from "../context/ApplicationUserContext";
import env from "../api/environment";
import { log } from "../util/logger";
import { AuthorizedItemsContext } from "../context/AuthorizedItemsContext";

interface LoginProps extends RouteComponentProps {}

function validate(username: string, password: string): boolean {
  return username.length > 0 && password.length > 5;
}

const Login: React.FC<LoginProps> = ({ history }: LoginProps) => {
  const { alertBoxJsx, showAlert } = useDialogBox();
  const { user, userLoggedIn, logout } = React.useContext(ApplicationUserContext);
  const { refreshAuth } = React.useContext(AuthorizedItemsContext);
  const [showLoading, setShowLoading] = useState(false);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [usernameErrors] = useState("");
  const [passwordErrors] = useState("");
  const [loginEnabled, setLoginEnabled] = useState(false);

  // ----------------------------------------------------------------------------------------------
  const onUsernameChange = (newUsername: string) => {
    setUsername(newUsername);
    setLoginEnabled(validate(newUsername, password));
  };

  // ----------------------------------------------------------------------------------------------
  const onPasswordChange = (newPassword: string) => {
    setPassword(newPassword);
    setLoginEnabled(validate(username, newPassword));
  };

  // ----------------------------------------------------------------------------------------------
  const login = async () => {
    if (!username || !password) {
      return;
    }

    const envUser = env.getUserAndEnvironmentFromInput(username);
    setShowLoading(true);
    UserAPI.authenticateUser(
      envUser.username,
      password,
      envUser.env.key
    ).then(response => {
      if (!response.loggedIn) {
        setShowLoading(false);
        log("login_failure", {
          username: envUser.username,
          appVersion: env.settings.appVersionString,
          environment: envUser.env.name,
        });
        const msg = response.error ? response.error : "Could not log in";
        showAlert("Failed to log in", msg, () => {
          setPassword("");
          setLoginEnabled(false);
        });

        return;
      }

      // user is logged in, get auth-item data;
      userLoggedIn(username, response.access_token, envUser.env.key).then(newUser => {
        refreshAuth(true, newUser).then(() => {
          log("login", {
            username: envUser.username,
            appVersion: env.settings.appVersionString,
            environment: envUser.env.name,
          });

          console.log(`logged in to ${envUser.env.name}: ${envUser.username}`);
          history.push("/tabs/schedule", { direction: "none" });
          history.goForward();
          setShowLoading(false);
        }).catch(() => {
          setShowLoading(false);
          showAlert("Failed to log in", "Could not retreive user data", () => {
            logout();
          })
        });
      }).catch(() => {
        setShowLoading(false);
        showAlert("Failed to log in", "Could not set access token", () => {
          logout();
        });
      });
    });
  }


  // ----------------------------------------------------------------------------------------------
/*

  const oldlogin = async () => {
    if (username && password) {
      const envUser = env.getUserAndEnvironmentFromInput(username);
      setShowLoading(true);
      const response = await UserAPI.authenticateUser(
        envUser.username,
        password,
        envUser.env.key
      );
      if (response.loggedIn) {
        const newUser = await userLoggedIn(
          username,
          response.access_token,
          envUser.env.key
        );
        await refreshAuth(true, newUser);
        log("login", {
          username: envUser.username,
          appVersion: env.settings.appVersionString,
          environment: envUser.env.name,
        });
        console.log(`logged in to ${envUser.env.name}: ${envUser.username}`);
        history.push("/tabs/schedule", { direction: "none" });
        history.goForward();
        setShowLoading(false);
      } else {
        setShowLoading(false);
        log("login_failure", {
          username: envUser.username,
          appVersion: env.settings.appVersionString,
          environment: envUser.env.name,
        });
        const msg = response.error ? response.error : "Could not log in";
        showAlert("Failed to log in", msg, () => {
          setPassword("");
          setLoginEnabled(false);
        });
      }
    }
  };
*/

  // ----------------------------------------------------------------------------------------------
  useIonViewWillEnter(() => {
    if (user.isLoggedin) {
      history.push("/tabs/schedule", { direction: "forward" });
      history.goForward();
    }
  });

  return (
    <IonPage id="login-page">
      <IonContent className="login-content">
        <div className="login-logo">
          <img
            className="logo"
            src="assets/img/OS Logo - 4pt Rounded _ 2 Tone Orange.svg"
            alt="Orbital Shift Logo"
          />
        </div>

        <IonLoading
          isOpen={showLoading}
          onDidDismiss={() => setShowLoading(false)}
          spinner="dots"
          message={"Logging in..."}
          duration={0}
        />

        {alertBoxJsx}

        <IonList className="form-list">
          <div className="item">
            <div className="label">User</div>
            <div className="field-container">
              <input
                className="input"
                id="username"
                name="username"
                type="text"
                value={username}
                onChange={(e) => {
                  onUsernameChange(e.target.value);
                }}
              />
            </div>
          </div>

          {usernameErrors.length > 0 && (
            <IonText color="light">
              <p className="ion-padding-start">{usernameErrors}</p>
            </IonText>
          )}

          <div className="item">
            <div className="label">Password</div>
            <div className="field-container">
              <input
                className="input"
                id="password"
                name="password"
                type="password"
                value={password}
                onChange={(e) => {
                  onPasswordChange(e.target.value);
                }}
              />
            </div>
          </div>

          {passwordErrors.length > 0 && (
            <IonText color="light">
              <p className="ion-padding-start">{passwordErrors}</p>
            </IonText>
          )}
        </IonList>

        <IonRow>
          <IonCol>
            <IonButton
              className="login-button"
              type="submit"
              onClick={login}
              expand="block"
              disabled={!loginEnabled}
            >
              LOG IN
            </IonButton>
          </IonCol>
        </IonRow>
      </IonContent>
    </IonPage>
  );
};

export default Login;
