import React, { useState, useEffect } from "react";
import {
  IonLabel,
  IonSelect,
  IonRow,
  IonCol,
  IonSelectOption,
} from "@ionic/react";
import "../../../global-styles.scss";
import "./EventTagEditor";
import { AuthorizedItems } from "../../../orbital-interfaces/authorized-items";
import { EditorValues, EditorReducerAction } from "../EditEventModalState";

interface EventTagEditorProps {
  auth: AuthorizedItems;
  editorValues: EditorValues;
  update: React.Dispatch<EditorReducerAction>;
}

interface TagList {
  id: number;
  name: string;
}

// --------------------------------------------------------------------------------------
const EventTagEditor: React.FC<EventTagEditorProps> = ({
  auth,
  editorValues,
  update,
}: EventTagEditorProps) => {
  const [selectedTagId, setSelectedTagId] = useState(-1);
  const [tagList, setTagList] = useState([] as TagList[]);

  // --------------------------------------------------------------------------------------
  useEffect(() => {
    if (editorValues.scheduleEventId === -1) {
      return;
    }

    if (auth && auth.locations && auth.locations.length > 0) {
      const tags = auth.tags
        .filter((t) =>
          t.positionIds.includes(editorValues.position.position.id)
        )
        .map((t) => {
          return { id: t.id, name: t.name };
        });
      setTagList(tags);

      const tagIds = tags.map((t) => t.id);
      if (!tagIds.includes(selectedTagId)) {
        if (
          editorValues.tag &&
          editorValues.tag.id &&
          tagIds.includes(editorValues.tag.id)
        ) {
          setSelectedTagId(editorValues.tag.id);
        } else {
          setSelectedTagId(-1);
        }
      }
    }

    // eslint-disable-next-line
  }, [auth, editorValues.position, editorValues.scheduleEventId]);

  const onUpdateTag = (tagId: number) => {
    setSelectedTagId(tagId);

    if (tagId === editorValues.tag.id) {
      return;
    }

    if (tagId === -1) {
      update({ type: "TAG-UPDATED", tag: { id: -1, name: "NONE" } });
    } else {
      const tag = tagList.filter((t) => t.id === tagId)[0];
      update({ type: "TAG-UPDATED", tag });
    }
  };

  if (tagList.length === 0) {
    return <></>;
  }

  return (
    <IonRow className="edit-row">
      <IonCol size="3">
        <IonLabel className="label">~Tag</IonLabel>
      </IonCol>
      <IonCol size="9">
        <IonSelect
          mode="ios"
          className="select"
          interface="action-sheet"
          disabled={editorValues.isReadonly}
          value={selectedTagId}
          onIonChange={(e) => {
            onUpdateTag(e.detail.value);
          }}
        >
          <IonSelectOption value={-1}>NONE</IonSelectOption>

          {tagList.map((tag) => (
            <IonSelectOption key={tag.id} value={tag.id}>
              {tag.name}
            </IonSelectOption>
          ))}
        </IonSelect>
      </IonCol>
    </IonRow>
  );
};

export default EventTagEditor;
