import React, { useContext, useEffect, useState } from "react";
import {
  IonButton,
  IonCol,
  IonContent,
  IonFooter,
  IonGrid,
  IonHeader,
  IonRow,
  IonSelect,
  IonSelectOption,
  IonTextarea,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
// import "./EventRequestModal.scss";
import "../../../global-styles.scss";
import {
  AuthorizedItems,
  LocationSettings,
} from "../../../orbital-interfaces/authorized-items";
import {
  EventEditAuthorization,
  EventEditEnum,
} from "../EditEventAuthorization";
import requestAPI, { CoverListItem } from "../../../api/RequestsAPI";
import { ApplicationUserContext } from "../../../context/ApplicationUserContext";

interface EventRequestModalProps {
  onCancel: () => void;
  onOk: (
    employeeId: number | null,
    notes: string,
    sendMessage: boolean
  ) => void;
  auth: AuthorizedItems;
  eventEditAuth: EventEditAuthorization;
  locSettings: LocationSettings;
  scheduleEventId: number;
}

// --------------------------------------------------------------------------------------
const EventRequestModal: React.FC<EventRequestModalProps> = ({
  onCancel,
  onOk,
  eventEditAuth,
  locSettings,
  scheduleEventId,
}: EventRequestModalProps) => {
  const { user } = useContext(ApplicationUserContext);
  const [notes, setNotes] = useState("" as string | null | undefined);
  const [selectedEmployeeId, setSelectedEmployeeId] = useState(
    null as number | null
  );
  const [coverList, setCoverList] = useState(null as CoverListItem[] | null);

  // --------------------------------------------------------------------------------------
  useEffect(() => {
    if (
      eventEditAuth.usage === EventEditEnum.CreateCoverRequest &&
      scheduleEventId > 0
    ) {
      requestAPI
        .getCoverRequestListItems(user, scheduleEventId, true)
        .then((results) => {
          setCoverList(results);
          if (results && results.length > 0) {
            setSelectedEmployeeId(results[0].value);
          }
        });
    }

    // eslint-disable-next-line
  }, [scheduleEventId, eventEditAuth.usage]);

  // --------------------------------------------------------------------------------------
  const onOkClick = () => {
    const newNotes = notes ?? "";
    onOk(selectedEmployeeId, newNotes, false);
  };

  if (eventEditAuth.usage === EventEditEnum.None) {
    return <div />;
  }

  return (
    <>
      <IonContent>
        <IonHeader mode="ios">
          <IonToolbar>
            <IonTitle>{eventEditAuth.buttonText}</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonGrid>
          <IonRow
            hidden={
              !locSettings.EnableDirectCoverRequest ||
              eventEditAuth.usage !== EventEditEnum.CreateCoverRequest
            }
          >
            <IonCol className="label" size="3">
              To
            </IonCol>
            <IonCol size="9">
              <IonSelect
                className="select"
                value={selectedEmployeeId}
                onIonChange={(e) => {
                  setSelectedEmployeeId(parseInt(e.detail.value));
                }}
              >
                {coverList &&
                  coverList.map((cover) => (
                    <IonSelectOption
                      key={cover.value}
                      value={cover.value}
                      disabled={cover.disabled}
                    >
                      {cover.text}
                    </IonSelectOption>
                  ))}
              </IonSelect>
            </IonCol>
          </IonRow>

          <IonRow
            hidden={eventEditAuth.usage === EventEditEnum.CreateCoverRequest}
          >
            <IonCol size="12">
              <div className="usage-message">{eventEditAuth.text}</div>
            </IonCol>
          </IonRow>

          <IonRow
            hidden={eventEditAuth.usage !== EventEditEnum.CreateCoverRequest}
          >
            <IonCol className="label" size="3">
              Notes
            </IonCol>
            <IonCol size="9">
              <IonTextarea
                className="select"
                maxlength={200}
                rows={4}
                value={notes}
                onIonChange={(e) => {
                  setNotes(e.detail.value);
                }}
              />
            </IonCol>
          </IonRow>

          <IonRow>
            <IonCol className="label" size="3" />
            <IonCol size="9" className="error-message">
              <div />
            </IonCol>
          </IonRow>
        </IonGrid>
      </IonContent>
      <IonFooter>
        <IonButton expand="block" color="secondary" onClick={onOkClick}>
          OK
        </IonButton>

        <IonButton expand="block" onClick={onCancel}>
          CANCEL
        </IonButton>
      </IonFooter>
    </>
  );
};

export default EventRequestModal;
