import {
  IonContent,
  IonHeader,
  IonIcon,
  IonItem,
  IonLabel,
  IonList,
  IonMenu,
  IonModal,
  IonMenuToggle,
  IonTitle,
  IonToolbar,
  IonFooter,
} from "@ionic/react";
import { settings, notifications, mail, logOut } from "ionicons/icons";
import React, { useState } from "react";
import "../global-styles.scss";
import { ApplicationUserContext } from "../context/ApplicationUserContext";
import env from "../api/environment";
import useDialogBox from "../hooks/useDialogBox";
import NotificationSettingsModal from "./NotificationSettingsModal";

const Menu: React.FC = () => {
  const [disableMenu] = useState(false);
  const { user } = React.useContext(ApplicationUserContext);
  const environment = env.settings.env.item(user.environment);
  const userName =
    environment.key !== "prod" ? user.username!.substr(4) : user.username!;
  const dlg = useDialogBox();
  const [showNotificationsModal, setShowNotificationsModal] = useState(false);

  /* tslint:disable:jsx-no-lambda */
  return (
    <IonMenu type="overlay" disabled={disableMenu} contentId="main">
      <IonHeader className="ion-no-border">
        <IonToolbar>
          <IonTitle>Menu</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent className="outer-content">
        {environment.key !== "prod" && (
          <div className="username-header">
            {userName.toUpperCase()} ({environment.name.toUpperCase()})
          </div>
        )}
        {environment.key === "prod" && <div className="username-header">User {userName}</div>}
        <IonList>
          <IonMenuToggle auto-hide="false">
            <IonItem button={true} routerLink="/account" routerDirection="none">
              <IonLabel>Settings</IonLabel>
              <IonIcon slot="end" icon={settings} />
            </IonItem>
          </IonMenuToggle>

          <IonMenuToggle auto-hide="false">
            <IonItem
              button={true}
              onClick={() => {
                setShowNotificationsModal(true);
              }}
            >
              <IonLabel>Notifications</IonLabel>
              <IonIcon slot="end" icon={notifications} />
            </IonItem>
          </IonMenuToggle>
          {/* KENKEN: TODO: Add 'My Account' and 'Legend' menu items in menu
          <IonMenuToggle auto-hide="false">
            <IonItem button={true} routerLink="/account" routerDirection="none">
              <IonLabel>My Account</IonLabel>
              <IonIcon slot="end" icon={person} />
            </IonItem>
          </IonMenuToggle>
          <IonMenuToggle auto-hide="false">
            <IonItem button={true} routerLink="/account" routerDirection="none">
              <IonLabel>Legend</IonLabel>
              <IonIcon slot="end" icon={colorPalette} />
            </IonItem>
          </IonMenuToggle>
          */}
          <IonMenuToggle auto-hide="false">
            <IonItem button={true} routerLink="/account" routerDirection="none">
              <IonLabel>Submit a Ticket</IonLabel>
              <IonIcon slot="end" icon={mail} />
            </IonItem>
          </IonMenuToggle>
          <IonMenuToggle auto-hide="false">
            <IonItem button={true} routerLink="/logout" routerDirection="none">
              <IonIcon slot="end" icon={logOut} />
              <IonLabel>Logout</IonLabel>
            </IonItem>
          </IonMenuToggle>
        </IonList>
        {dlg.alertBoxJsx}
      </IonContent>
      <IonFooter>
        <div className="debug-info">
          Capacitor: {env.settings.hasCapacitor ? "Yes" : "No"}
          <br />
          Build Date: {env.settings.buildDate}
          <br />
          Version: {env.settings.appVersionString}
        </div>
      </IonFooter>

      <IonModal isOpen={showNotificationsModal}>
        <NotificationSettingsModal
          onDismissModal={() => {
            setShowNotificationsModal(false);
          }}
          isOpen={showNotificationsModal}
        />
      </IonModal>
    </IonMenu>
  );
};

export default Menu;
