import React, { useState, useEffect } from "react";
// import "./ClockPanel.scss";
import "../../global-styles.scss";
import { addDays, parseISO } from "date-fns";
import { ApplicationUserContext } from "../../context/ApplicationUserContext";
import { AuthorizedItemsContext } from "../../context/AuthorizedItemsContext";
import PayrollPeriodChooser from "./PayrollPeriodChooser";
import PayrollPeriodAPI, {
  PayrollPeriodData,
} from "../../api/PayrollPeriodAPI";
import useDialogBox from "../../hooks/useDialogBox";
import PayrollDetailList from "./PayrollDetailList";
import { RefresherEventDetail } from "@ionic/core";
import useIonicRefresher from "../../hooks/useIonicRefresher";
import { IonContent } from "@ionic/react";

interface PayrollPeriodsTabProps {
  date: Date | null;
}

const PayrollPeriodsTab: React.FC<PayrollPeriodsTabProps> = ({
  date,
}: PayrollPeriodsTabProps) => {
  const { user } = React.useContext(ApplicationUserContext);
  const { auth } = React.useContext(AuthorizedItemsContext);
  const [currentDate, setCurrentDate] = useState(null as Date | null);
  const [startDate, setStartDate] = useState(null as Date | null);
  const [endDate, setEndDate] = useState(null as Date | null);
  const [payrollData, setPayrollData] = useState(
    null as PayrollPeriodData | null
  );
  const dlg = useDialogBox();

  // ------------------------------------------------------------------------------------
  const doRefresh = (e: CustomEvent<RefresherEventDetail>) => {
    getData(e);
  };

  const refresher = useIonicRefresher(doRefresh);

  // ------------------------------------------------------------------------------------
  useEffect(() => {
    let newStartDate = date;
    if (date === null) {
      newStartDate = new Date();
    }

    setCurrentDate(newStartDate);
  }, [date]);

  // ------------------------------------------------------------------------------------
  useEffect(() => {
    if (currentDate == null) {
      return;
    }

    getData();
    // eslint-disable-next-line
  }, [currentDate]);

  // ------------------------------------------------------------------------------------
  function getData(e?: CustomEvent<RefresherEventDetail>) {
    PayrollPeriodAPI.getPayPeriodDateForEmployee(
      user,
      auth.locations[0].id,
      currentDate!
    ).then((data) => {
      if (data.error) {
        dlg.showAlert(
          "Error",
          "Could not retrieve Payroll data: " + data.message,
          () => {}
        );
        return;
      }

      setStartDate(parseISO(data.start));
      setEndDate(parseISO(data.end));
      setPayrollData(data);
      if (e) {
        e.detail.complete();
      }
    });
  }

  // ------------------------------------------------------------------------------------
  const onNextPayPeriod = () => {
    setCurrentDate(addDays(endDate!, 1));
  };

  // ------------------------------------------------------------------------------------
  const onPrevPayPeriod = () => {
    setCurrentDate(addDays(startDate!, -1));
  };

  return (
    <>
      <PayrollPeriodChooser
        startDate={startDate}
        endDate={endDate}
        onNextPayPeriod={onNextPayPeriod}
        onPrevPayPeriod={onPrevPayPeriod}
      />
      <IonContent>
        {refresher.ionicRefresherJsx}
        {auth && auth.locations && auth.locations.length > 0 && (
          <PayrollDetailList payrollData={payrollData} />
        )}
        {dlg.alertBoxJsx}
      </IonContent>
    </>
  );
};

export default PayrollPeriodsTab;
