import { IonAlert } from "@ionic/react";
import React, { useState } from "react";

function useDialogBox() {
  const [isOpen, setIsOpen] = useState(false);
  const [alertData, setAlertData] = useState({
    header: "",
    message: "",
    onDismiss: () => {},
  });

  // ------------------------------------------------------------------------------------
  const showAlert = (
    header: string,
    message: string,
    onDismiss: () => void
  ) => {
    setAlertData({
      header,
      message,
      onDismiss,
    });

    setIsOpen(true);
  };

  const alertBoxJsx = (
    <IonAlert
      isOpen={isOpen}
      onDidDismiss={() => {
        setIsOpen(false);
        alertData.onDismiss();
      }}
      header={alertData.header}
      message={alertData.message}
      buttons={["OK"]}
    />
  );

  return {
    showAlert,
    alertBoxJsx,
  };
}

export default useDialogBox;
