import { ApplicationUser } from "../context/ApplicationUserContext";
import apiHelper from "./OrbitalApiHelper";
import { DeviceOperatingSystem } from "../enums/DeviceOperatingSystem";
import { NotificationTypeModel } from "../orbital-interfaces/NotificationTypeModel";
import env from "./environment";
import { log } from "../util/logger";

// ----------------------------------------------------------------------------------------------
const subscribe = async (user: ApplicationUser, token: string) => {
  const url = `api/v1/employee/notifications/subscribe`;
  const operatingSystem =
    env.settings.platform === "ios"
      ? DeviceOperatingSystem.iOS
      : DeviceOperatingSystem.Android;

  log("pushNotifications_subscribeCalled", {
    token,
    operatingSystem,
    username: user.username,
    access_token: user.access_token,
  });

  return await apiHelper.putData(user, url, {
    TokenId: token,
    OperatingSystem: operatingSystem,
  });
};

// ----------------------------------------------------------------------------------------------
const unsubscribe = async (user: ApplicationUser, token: string) => {
  const url = `api/v1/employee/notifications/unsubscribe`;
  const operatingSystem =
    env.settings.platform === "ios"
      ? DeviceOperatingSystem.iOS
      : DeviceOperatingSystem.Android;

  return await apiHelper.putData(user, url, {
    TokenId: token,
    OperatingSystem: operatingSystem,
  });
};

// ----------------------------------------------------------------------------------------------
const getTypesSelectedByUser = async (user: ApplicationUser) => {
  const url = `api/v1/employee/notifications/types`;
  return (await apiHelper.get(user, url)) as NotificationTypeModel[];
};

// ----------------------------------------------------------------------------------------------
const putTypes = async (user: ApplicationUser, typeIds: number[]) => {
  const url = `api/v1/employee/notifications/types`;
  const data = {
    ids: typeIds,
  };

  return (await apiHelper.put(user, url, data)) as void;
};

// ----------------------------------------------------------------------------------------------
const listTypes = async (user: ApplicationUser) => {
  const url = `api/v1/employee/notifications/list-types`;

  // NOTE: the API no longer really uses passed-in data since the
  // "Communications rewrite"

  return (await apiHelper.get(user, url)) as NotificationTypeModel[];
};

export default {
  subscribe,
  unsubscribe,
  getTypesSelectedByUser,
  putTypes,
  listTypes,
};
