import React, { useState } from "react";
import {
  IonButton,
  IonButtons,
  IonIcon,
  IonContent,
  IonFooter,
  IonHeader,
  IonItem,
  IonLabel,
  IonSelect,
  IonSelectOption,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import { arrowBackOutline } from "ionicons/icons";
// import "../SchedulePageComponents/ScheduleFilterModal.scss";
import "../../global-styles.scss";
import ToggleListEx from "../ToggleListEx";
import { NotificationTypeModel } from "../../orbital-interfaces/NotificationTypeModel";

export interface FilterData {
  msgTypes: { messageTypeId: number; name: string }[];
  employees: { employeeId: number; name: string }[];
}

export interface FilterResult {
  msgTypeIds: number[];
  employeeId: number;
}

interface MessagesFilterModalProps {
  onDismissModal: (result: FilterResult) => void;
  onCancelModal: () => void;
  filterData: FilterData;
}

const MessagesFilterModal: React.FC<MessagesFilterModalProps> = ({
  onDismissModal,
  onCancelModal,
  filterData,
}: MessagesFilterModalProps) => {
  const [filterResult, setFilterResult] = useState({
    msgTypeIds: filterData.msgTypes
      ? filterData.msgTypes.map((mt) => mt.messageTypeId)
      : ([] as number[]),
    employeeId: -1,
  } as FilterResult);

  // --------------------------------------------------------------------------------------
  const onCloseModal = () => {
    onDismissModal(filterResult);
  };

  // --------------------------------------------------------------------------------------
  const onCancel = () => {
    onCancelModal();
  };

  // --------------------------------------------------------------------------------------
  const onResetAll = () => {
    setFilterResult({
      msgTypeIds: filterData.msgTypes.map((mt) => mt.messageTypeId),
      employeeId: -1,
    });
  };

  // --------------------------------------------------------------------------------------
  const onMessageTypeChange = (newSelection: NotificationTypeModel[]) => {
    setFilterResult({
      ...filterResult,
      msgTypeIds: newSelection.map((mt) => mt.id),
    });
  };

  return (
    <>
      <IonHeader className="ion-no-border">
        <IonToolbar>
          <IonButtons slot="start">
            <IonButton onClick={onCancel}>
              <IonIcon icon={arrowBackOutline} mode="md" slot="icon-only" />
            </IonButton>
          </IonButtons>
          <IonTitle>Filter</IonTitle>
          <IonButtons slot="end">
            <IonButton onClick={onResetAll}>RESET ALL</IonButton>
          </IonButtons>
        </IonToolbar>
      </IonHeader>

      <IonContent class="outer-content">
        <IonItem>
          <IonLabel>From</IonLabel>
          <IonSelect
            value={filterResult.employeeId}
            onIonChange={(e) =>
              setFilterResult({ ...filterResult, employeeId: e.detail.value })
            }
            interface="action-sheet"
          >
            <IonSelectOption key={-1} value={-1}>
              EVERYONE
            </IonSelectOption>
            {filterData &&
              filterData.employees &&
              filterData.employees.map((emp) => (
                <IonSelectOption key={emp.employeeId} value={emp.employeeId}>
                  {emp.name}
                </IonSelectOption>
              ))}
          </IonSelect>
        </IonItem>
        <IonItem>
          <IonLabel>Message Types</IonLabel>
        </IonItem>
        {filterData.msgTypes && (
          <ToggleListEx
            list={filterData.msgTypes.map((mt) => {
              return { id: mt.messageTypeId, name: mt.name, description: "" };
            })}
            selected={filterData.msgTypes
              .filter((mt) =>
                filterResult.msgTypeIds.includes(mt.messageTypeId)
              )
              .map((mt) => {
                return { id: mt.messageTypeId, name: mt.name, description: "" };
              })}
            selectionChanged={onMessageTypeChange}
          />
        )}
      </IonContent>

      <IonFooter>
        <IonButton
          className="apply-button"
          expand="block"
          onClick={onCloseModal}
        >
          APPLY FILTER
        </IonButton>
        <IonButton
          className="apply-button"
          expand="block"
          onClick={onCancel}
          color="tertiary"
        >
          BACK
        </IonButton>
      </IonFooter>
    </>
  );
};

export default MessagesFilterModal;
