import React, { useState } from "react";
import {
  IonFooter,
  IonSegment,
  IonSegmentButton,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonPage,
  IonButtons,
  IonMenuButton,
} from "@ionic/react";
// import "./ClockPage.scss";
import "../global-styles.scss";
import ClockPanel from "../components/ClockPageComponents/ClockPanel";
import PayrollPeriodsTab from "../components/ClockPageComponents/PayrollPeriodsTab";
import ClockTodayTab from "../components/ClockPageComponents/ClockTodayTab";

const ClockPage: React.FC = () => {
  const [segment, setSegment] = useState<"today" | "pay-periods">("today");
  const [refreshToggle, setRefreshToggle] = useState(false);
  const [refreshTimeClockToggle, setRefreshTimeClockToggle] = useState(false);

  // ------------------------------------------------------------------------------------
  const onClockPanelUpdate = () => {
    setRefreshToggle(!refreshToggle);
  };

  // ------------------------------------------------------------------------------------
  const onTodayPanelUpdate = () => {
    console.log("onTodayPanelUpdate");
    setRefreshTimeClockToggle(!refreshTimeClockToggle);
  };

  // ------------------------------------------------------------------------------------
  const onSegmentChange = (segmentName: any) => {
    setSegment(segmentName);
  };

  return (
    <IonPage>
      <IonHeader className="ion-no-border">
        <IonToolbar mode="md">
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle slot="start">Clock</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonSegment
        mode="md"
        onIonChange={(e) => onSegmentChange(e.detail.value)}
      >
        <IonSegmentButton mode="md" value="today">
          TODAY
        </IonSegmentButton>

        <IonSegmentButton mode="md" value="pay-periods">
          PAY PERIOD
        </IonSegmentButton>
      </IonSegment>

      {segment === "today" && (
        <ClockTodayTab
          refreshToggle={refreshToggle}
          onUpdate={onTodayPanelUpdate}
        />
      )}

      {segment === "pay-periods" && <PayrollPeriodsTab date={null} />}

      <IonFooter>
        {segment === "today" && (
          <ClockPanel
            refreshToggle={refreshTimeClockToggle}
            onUpdate={onClockPanelUpdate}
          />
        )}
      </IonFooter>
    </IonPage>
  );
};

export default ClockPage;
