import React from "react";
import {IonContent, IonLoading} from "@ionic/react";
import "../global-styles.scss";
import env from '../api/environment';

const SplashComponent: React.FC = () => {
  return (
    <>
      <IonContent id="splash">
        <div className="login-logo">
          <img
            className="logo"
            src="assets/img/OS Logo - 4pt Rounded _ 2 Tone Orange.svg"
            alt="Orbital Shift Logo"
          />
        </div>


        <IonLoading
          isOpen={true}
          spinner="dots"
          message={"Loading..."}
          showBackdrop={false}
          duration={0}
        />


        <footer>
          {env.settings.appVersionString} ({env.settings.buildDate})
        </footer>
      </IonContent>
    </>
  );
};

export default SplashComponent;
