import React, { useContext } from "react";
import {
  IonContent,
  IonRow,
  IonCol,
  IonButtons,
  IonButton,
  IonIcon,
  IonGrid,
  IonHeader,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import "../../global-styles.scss";
import { close } from "ionicons/icons";
import { parseISO } from "date-fns";
import { PayrollPeriodDateItem } from "../../api/PayrollPeriodAPI";
import { getDateString } from "../../util/time-helper";
import {
  AuthorizedItems,
  LocationSettings,
} from "../../orbital-interfaces/authorized-items";
import PayrollOrbitalTimeItem from "./PayrollOrbitalTimeItem";
import { AuthorizedItemsContext } from "../../context/AuthorizedItemsContext";

interface AddBreakModalProps {
  onClose: () => void;
  data: PayrollPeriodDateItem;
  locSettings: LocationSettings;
}

function getAbsentName(auth: AuthorizedItems, id: number) {
  return auth.payrollEvents.filter((pe) => pe.id === id)[0].name;
}

// --------------------------------------------------------------------------------------
const PayrollDetailModal: React.FC<AddBreakModalProps> = ({
  onClose,
  data,
  locSettings,
}: AddBreakModalProps) => {
  const { auth } = useContext(AuthorizedItemsContext);

  if (!data) {
    return <div>loading</div>;
  }

  return (
    <>
      <IonHeader mode="ios">
        <IonToolbar>
          <IonTitle>{getDateString(parseISO(data.date))}</IonTitle>
          <IonButtons slot="end">
            <IonButton onClick={onClose}>
              <IonIcon icon={ close } mode="ios" slot="icon-only" />
            </IonButton>
          </IonButtons>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <IonGrid>
          {data.regular.ActualHours > 0.0 && (
            <IonRow>
              <IonCol size="7">Regular</IonCol>
              <IonCol size="5">
                <PayrollOrbitalTimeItem
                  time={data.regular}
                  locSettings={locSettings}
                  fontColor="black"
                />
              </IonCol>
            </IonRow>
          )}

          {data.dailyOvertime.ActualHours > 0.0 && (
            <IonRow>
              <IonCol size="7">Daily Overtime</IonCol>
              <IonCol size="5">
                <PayrollOrbitalTimeItem
                  time={data.dailyOvertime}
                  locSettings={locSettings}
                  fontColor="red"
                />
              </IonCol>
            </IonRow>
          )}

          {data.weeklyOvertime.ActualHours > 0.0 && (
            <IonRow>
              <IonCol size="7">Weekly Overtime</IonCol>
              <IonCol size="5">
                <PayrollOrbitalTimeItem
                  time={data.weeklyOvertime}
                  locSettings={locSettings}
                  fontColor="red"
                />
              </IonCol>
            </IonRow>
          )}

          {data.special.ActualHours > 0.0 && (
            <IonRow>
              <IonCol size="7">Special Dates</IonCol>
              <IonCol size="5">
                <PayrollOrbitalTimeItem
                  time={data.special}
                  locSettings={locSettings}
                  fontColor="black"
                />
              </IonCol>
            </IonRow>
          )}

          {data.timePerAbsent &&
            data.timePerAbsent.map((hpa) => {
              return (
                <IonRow key={hpa.absentId}>
                  <IonCol size="7">{getAbsentName(auth, hpa.absentId)}</IonCol>
                  <IonCol size="5">
                    <PayrollOrbitalTimeItem
                      time={hpa.time}
                      locSettings={locSettings}
                      fontColor="#AD850C"
                    />
                  </IonCol>
                </IonRow>
              );
            })}
        </IonGrid>
      </IonContent>
    </>
  );
};

export default PayrollDetailModal;
