import React, { useContext, useState } from "react";
import { IonModal } from "@ionic/react";
import EditDailyNoteModal from "./EditDailyNoteModal";
import { AuthorizedItemsContext } from "../../context/AuthorizedItemsContext";
import {
  AuthorizedItems,
  Role,
} from "../../orbital-interfaces/authorized-items";

interface AddDailyNoteButtonProps {
  date: Date;
  onForceRefresh: (toastMessage: string) => void;
}

// --------------------------------------------------------------------------------------
function canAddNote(auth: AuthorizedItems) {
  if (auth.role === Role.Employee) {
    return false;
  }

  if (auth.role === Role.BusinessAdmin || auth.role === Role.LocationAdmin) {
    return true;
  }

  for (let i = 0; i < auth.locations.length; i++) {
    const curLoc = auth.locations[i];
    if (curLoc.adminAllDepts) {
      return true;
    }

    for (let j = 0; j < curLoc.departments.length; j++) {
      if (curLoc.departments[j].isDeptAdmin) {
        return true;
      }
    }
  }

  return false;
}

// --------------------------------------------------------------------------------------
//
// --------------------------------------------------------------------------------------
const AddDailyNoteButton: React.FC<AddDailyNoteButtonProps> = ({
  date,
  onForceRefresh,
}) => {
  const [showEditModal, setShowEditModal] = useState(false);
  const { auth } = useContext(AuthorizedItemsContext);

  if (!canAddNote(auth)) {
    return <span />;
  }

  const onClose = (toastMessage: string) => {
    setShowEditModal(false);
    onForceRefresh(toastMessage);
  };

  return (
    <>
      <button
        className="add-daily-note-button"
        onClick={() => {
          setShowEditModal(true);
        }}
      >
        + Daily Note
      </button>

      <IonModal
        isOpen={showEditModal}
        onDidDismiss={() => setShowEditModal(false)}
      >
        <EditDailyNoteModal
          isOpen={showEditModal}
          note={null}
          initialDate={date}
          onCloseModal={(toast) => {
            onClose(toast);
          }}
          newNoteClicked={true}
        />
      </IonModal>
    </>
  );
};

export default AddDailyNoteButton;
