/* eslint camelcase: 0 */
import {
  HomePageDataGet,
  AuthorizedItems,
} from "../orbital-interfaces/authorized-items";
import { ApplicationUser } from "../context/ApplicationUserContext";
import env from "./environment";
import apiHelper from "./OrbitalApiHelper";

interface AccessTokenResponse {
  access_token: string;
  token_type: string;
  expires_in: number;
  error?: string;
  error_description?: string;
  [".issued"]: string;
  [".expires"]: string;
}

// ----------------------------------------------------------------------------------------------
const authenticateUser = async (
  username: string,
  password: string,
  environment: string
) => {
  console.log(
    `Authenticating against ${env.settings.env.item(environment).name} (${
      env.settings.env.item(environment).url
    })`
  );
  const url = env.settings.env.item(environment).url + "token";
  const body = `grant_type=password&username=${username}&password=${password}`;
  try {
    const response = await fetch(url, {
      method: "POST",
      body: body,
    });

    const json = (await response.json()) as AccessTokenResponse;
    if (json.error || json.error_description) {
      return {
        loggedIn: false,
        error: json.error_description,
        access_token: "",
      };
    }

    return { loggedIn: true, access_token: json.access_token };
  } catch (err) {
    console.error("Error returned from authentication request: " + err);
    return {
      loggedIn: false,
      err,
      error: "Error attempting to log in",
      access_token: "",
    };
  }
};

// ----------------------------------------------------------------------------------------------
const getHomePageData = async (user: ApplicationUser) => {
  return (await apiHelper.get(user, "api/v3/home/get/0")) as HomePageDataGet;
};

// ----------------------------------------------------------------------------------------------
const getAuthorizedItems = async (user: ApplicationUser) => {
  const result = (await apiHelper.get(
    user,
    "api/v3/home/get-auth"
  )) as AuthorizedItems;
  return result;
};

export default {
  authenticateUser,
  getHomePageData,
  getAuthorizedItems,
};
