import { ApplicationUser } from "../context/ApplicationUserContext";
import apiHelper from "./OrbitalApiHelper";
import { formatDateTimeAsMvcString } from "../util/time-helper";
import { DailyNotes } from "../orbital-interfaces/DailyNotes";

export interface ApiResult {
  error: boolean;
  message: string;
}

// ----------------------------------------------------------------------------------------------
const updateDailyNote = async (
  user: ApplicationUser,
  dailyNoteId: number,
  text: string,
  role: number,
  date: Date
) => {
  const result = (await apiHelper.post(user, "api/v2/daily-notes/update", {
    dailyNoteId,
    date: formatDateTimeAsMvcString(date),
    text,
    role,
  })) as ApiResult;

  return result;
};

// ----------------------------------------------------------------------------------------------
const insertDailyNote = async (
  user: ApplicationUser,
  locId: number,
  depId: number,
  role: number,
  date: Date,
  text: string
) => {
  const result = (await apiHelper.post(user, "api/v2/daily-notes/insert", {
    locId,
    depId,
    role,
    date,
    text,
  })) as ApiResult;

  return result;
};

// ----------------------------------------------------------------------------------------------
const deleteDailyNote = async (user: ApplicationUser, dailyNoteId: number) => {
  const result = (await apiHelper.post(
    user,
    "api/v2/daily-notes/delete/" + dailyNoteId
  )) as ApiResult;

  return result;
};

// ----------------------------------------------------------------------------------------------
const getDailyNote = async (
  user: ApplicationUser,
  locId: number,
  depId: number,
  role: number,
  date: Date
): Promise<ApiResult | DailyNotes> => {
  const result = (await apiHelper.post(user, "api/v2/daily-notes/get", {
    locId,
    depId,
    role,
    date,
  })) as ApiResult | DailyNotes;

  return result;
};

export default {
  getDailyNote,
  updateDailyNote,
  insertDailyNote,
  deleteDailyNote,
};
