import { EventBreak } from "../orbital-interfaces/EventBreak";
import { EventInformation } from "../orbital-interfaces/TEC";
import { ScheduleEvent } from "../orbital-interfaces/ScheduleEvent";
import {
  addDays,
  differenceInDays,
  differenceInMinutes,
  format,
  isAfter,
  isSameYear,
  isToday,
  parseISO,
} from "date-fns";
import {
  LocationSettings,
  TimeFormat,
} from "../orbital-interfaces/authorized-items";
import { OrbitalTime } from "../orbital-interfaces/OrbitalTime";
import {isClockedIn} from "./scheduleevent-helper";
import {toDate, format as tzFormat} from "date-fns-tz";

const noEndTime = new Date(9000, 1, 1);

function isScheduleEvent(ev: EventInformation | ScheduleEvent): ev is ScheduleEvent {
  return (ev as ScheduleEvent).BusinessId !== undefined;
}

// ---------------------------------------------------------------------------------------------
export function convertTimeWithoutTimeZoneToLocalTime(dtStr: string): string {
  const localTimeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;
  const newDate = toDate(dtStr, { timeZone: localTimeZone});
  const dateOnlyStr = tzFormat(newDate,'yyyy-MM-dd', { timeZone: localTimeZone});
  const timeOnlyStr = tzFormat(newDate,'HH:mm:ssXXX', { timeZone: localTimeZone});
  return dateOnlyStr + "T" + timeOnlyStr;
}

// ---------------------------------------------------------------------------------------------
export function getDurationFromTimeSpan(timespan: string) {
  if (!timespan) {
    return 0;
  }

  const tok = timespan.split(":");
  let hrs: number;
  let min: number;
  if (tok[0].indexOf(".") >= 0) {
    const hrTok = tok[0].split(".");
    hrs = 24 * parseInt(hrTok[0]) + parseInt(hrTok[1]);
    min = parseInt(tok[1]);
  } else {
    hrs = parseInt(tok[0]);
    min = parseInt(tok[1]);
  }

  return hrs * 60 + min;
}

// ---------------------------------------------------------------------------------------------
export function messageDateString(time: string) {
  const date = parseISO(time);
  const now = new Date();

  if (isToday(date)) {
    return format(date, "h:mmaaa").replace("M", "");
  } else if (differenceInDays(now, date) < 7) {
    return format(date, "EEE");
  } else if (isSameYear(date, now)) {
    return format(date, "MMM d");
  } else {
    return format(date, "M/d/yyyy");
  }
}

// ---------------------------------------------------------------------------------------------
export function getTimeStringFromMinutes(
  locSettings: LocationSettings,
  min: number | null
) {
  if (locSettings.TimeFormat === TimeFormat.HoursMinutes) {
    if (min === null) {
      return "00:00";
    }

    const hours = Math.floor(min / 60);
    const minutes = min % 60;

    return `${padZeroes(hours, 2)}:${padZeroes(minutes, 2)}`;
  } else {
    if (min === null) {
      return "0";
    }

    return formatDecimal(
      min / 60,
      locSettings.TimeFormatDigits,
      locSettings.TimeFormatDecimals
    );
  }
}

// ---------------------------------------------------------------------------------------------
export function getTimeStringFromOrbitalTime(
  locSettings: LocationSettings,
  time: OrbitalTime
) {
  if (time.ActualTime === "00:00:00") {
    return locSettings.TimeFormat === TimeFormat.HoursMinutes
      ? "00:00"
      : formatDecimal(
          0,
          locSettings.TimeFormatDigits,
          locSettings.TimeFormatDecimals
        );
  }

  if (locSettings.TimeFormat === TimeFormat.HoursMinutes) {
    const tok = time.ActualTime.split(":");
    if (tok.length !== 3) {
      return time.ActualTime;
    }

    if (tok[0].includes(".")) {
      const dayHourTok = tok[0].split(".");
      const hours = 24 * parseInt(dayHourTok[0]) + parseInt(dayHourTok[1]);
      return `${hours}:${tok[1]}`;
    } else {
      const hours = parseInt(tok[0]);
      return `${hours}:${tok[1]}`;
    }
  } else {
    return formatDecimal(
      time.ActualHours,
      locSettings.TimeFormatDigits,
      locSettings.TimeFormatDecimals
    );
  }
}

// ---------------------------------------------------------------------------------------------
export function getMinutesFromTimeString(
  locSettings: LocationSettings,
  str: string
) {
  if (!str || str.length === 0) {
    return 0;
  }

  if (locSettings.TimeFormat === TimeFormat.HoursMinutes) {
    const hourMinRegEx = /^\d*(:\d{2})?$/;
    if (!hourMinRegEx.test(str)) {
      return 0;
    }

    if (str[0] === ":") {
      return parseInt(str.substring(1));
    }

    const tok = str.split(":");
    if (tok.length === 1) {
      return parseInt(str) * 60;
    }

    return parseInt(tok[0]) * 60 + parseInt(tok[1]);
  } else {
    const decimalRegex = /^\d+(.\d*)?$/;
    if (!decimalRegex.test(str)) {
      return 0;
    }

    return Math.round(parseFloat(str) * 60.0);
  }
}

// ---------------------------------------------------------------------------------------------
export function getDurationStringFromTimeSpan(timespan: string) {
  if (!timespan) {
    return "(0m)";
  }
  const tok = timespan.split(":");
  let hrs: number;
  let min: number;
  if (tok[0].indexOf(".") >= 0) {
    const hrTok = tok[0].split(".");
    hrs = 24 * parseInt(hrTok[0]) + parseInt(hrTok[1]);
    min = parseInt(tok[1]);
  } else {
    hrs = parseInt(tok[0]);
    min = parseInt(tok[1]);
  }

  return hrs > 0 ? `(${hrs}h ${min}m)` : `(${min}m)`;
}

// ---------------------------------------------------------------------------------------------
export function getDurationString(minutes: number): string {
  if (minutes < 60) {
    return `(${minutes}m)`;
  }

  const str = `(${Math.floor(minutes / 60)}h`;
  return minutes % 60 === 0 ? str + ")" : str + ` ${minutes % 60}m)`;
}

// ---------------------------------------------------------------------------------------------
export function getDurationFromScheduleEvent(
  ev: EventInformation | ScheduleEvent
): number {
  if ((ev as EventInformation).Data) {
    return getDurationFromTimeSpan(
      (ev as EventInformation).Data!.HourlyValue.Time.ActualTime
    );
  }

  const getDiff = (start: string, end?: string) => {
    const startDate = parseISO(start);
    let endDate = end ? parseISO(end) : new Date();
    if (endDate > noEndTime) {
      endDate = new Date();
    }
    return differenceInMinutes(endDate, startDate);
  };

  let totalMinutes = getDiff(ev.EventStart, ev.EventEnd);
  if (ev.EventBreaks) {
    ev.EventBreaks.map(
      (b) => (totalMinutes -= getDiff(b.BreakStart, b.BreakEnd))
    );
  }

  return totalMinutes;
}

// ---------------------------------------------------------------------------------------------
export function getDuration(ev: EventInformation): number {
  const getDiff = (start: string, end?: string) => {
    const startDate = parseISO(start);
    let endDate = end ? parseISO(end) : new Date();
    if (endDate > noEndTime) {
      endDate = new Date();
    }
    return differenceInMinutes(endDate, startDate);
  };

  let totalMinutes = getDiff(ev.EventStart, ev.EventEnd);
  if (ev.EventBreaks) {
    ev.EventBreaks.map(
      (b) => (totalMinutes -= getDiff(b.BreakStart, b.BreakEnd))
    );
  }

  return totalMinutes;
}

// ---------------------------------------------------------------------------------------------
export function getTotalDuration(events: EventInformation[]) {
  let totalMinutes = 0;
  events.map((ev) => (totalMinutes += getDuration(ev)));
  return totalMinutes;
}

// ---------------------------------------------------------------------------------------------
export function getTotalDurationFromScheduleEvent(events: ScheduleEvent[]) {
  let totalMinutes = 0;
  events.map((ev) => (totalMinutes += getDurationFromScheduleEvent(ev)));
  return totalMinutes;
}

// ---------------------------------------------------------------------------------------------
export function getDateString(date: Date): string {
  return format(date, "EEE MMM d, yyyy").toUpperCase();
}

// ---------------------------------------------------------------------------------------------
export function getStartTime(ev: EventInformation | ScheduleEvent) {
  return format(parseISO(ev.EventStart), "h:mmaaa").replace("M", "");
}

// ---------------------------------------------------------------------------------------------
export function getMessageDeliveryTimeString(dateStr: string) {
  return format(parseISO(dateStr), "M/d EEE h:mmaa").replace("M", "");
}

// ---------------------------------------------------------------------------------------------
export function getBreakStartTime(brk: EventBreak) {
  return format(parseISO(brk.BreakStart), "h:mmaaa").replace("M", "");
}

// ---------------------------------------------------------------------------------------------
export function getEventStartEnd(ev: EventInformation | ScheduleEvent) {
  const start = format(parseISO(ev.EventStart), "h:mmaaa").replace("M", "");
  const end = format(parseISO(ev.EventEnd), "h:mmaaa").replace("M", "");

  return `${start} - ${end}`;
}

// ---------------------------------------------------------------------------------------------
export function getBreakStartEnd(brk: EventBreak) {
  const start = format(parseISO(brk.BreakStart), "h:mmaaa").replace("M", "");

  if (!brk.BreakEnd || brk.BreakEnd > noEndTime) {
    return start;
  }

  const end = format(parseISO(brk.BreakEnd), "h:mmaaa").replace("M", "");
  return `${start} - ${end}`;
}
// ---------------------------------------------------------------------------------------------
export function getEventInformationStartEnd(
  ev: EventInformation | ScheduleEvent
) {
  const start = format(parseISO(ev.EventStart), "h:mmaaa").replace("M", "");
  let end: string;

  if (!isScheduleEvent(ev) && ev.IsClockedIn) {
    end = format(new Date(),"h:mmaaa").replace("M", "");
  } else  if (isScheduleEvent(ev) && isClockedIn(ev)) {
    end = format(new Date(), "h:mmaaa").replace("M", "");
  } else {
    end = format(parseISO(ev.EventEnd), "h:mmaaa").replace("M", "");
  }

  return `${start} - ${end}`;
}

// ---------------------------------------------------------------------------------------------
export function getEventBreakStartEndString(brk: EventBreak): string {
  const start = format(parseISO(brk.BreakStart), "h:mmaa").replace("M", "");
  const end = brk.BreakEnd
    ? format(parseISO(brk.BreakEnd), "h:mmaa").replace("M", "")
    : format(new Date(), "h:mmaa").replace("M", "");

  return `${start} - ${end}`;
}

// ---------------------------------------------------------------------------------------------
export function getDateOnlyFromISOString(str: string) {
  const date = parseISO(str);
  const dateOnlyStr = format(date, "yyyy-MM-dd");
  return parseISO(dateOnlyStr);
}

// ---------------------------------------------------------------------------------------------
export function getWeekStart(
  date: Date,
  weekStartsOn: string,
  dayStart: string
): Date {
  const dayStartMinutes =
    !dayStart || dayStart.indexOf("T") < 0
      ? 0
      : getDurationFromTimeSpan(dayStart.split("T")[1]);

  let newDate = date;
  newDate.setHours(0, dayStartMinutes, 0, 0);

  let counter = 0;
  while (format(newDate, "EEEE") !== weekStartsOn && counter < 20) {
    newDate = addDays(newDate, -1);
    counter++;
  }

  if (counter > 15) {
    console.error(
      `Could not calculate week start based on date ${date}` +
        `and weekStartsOn "${weekStartsOn}"`
    );
  }

  return newDate;
}

// ---------------------------------------------------------------------------------------------
export function getDayStartMinutes(dayStartLocSetting: string) {
  const tok = dayStartLocSetting.split("T");
  return getDurationFromTimeSpan(tok[1]);
}

// ---------------------------------------------------------------------------------------------
export function getTimeDiff(dateStr: string) {
  const now = new Date();
  const dateTime = parseISO(dateStr);
  let minutes = isAfter(now, dateTime)
    ? differenceInMinutes(now, dateTime)
    : differenceInMinutes(dateTime, now);

  const hours = parseInt("" + minutes / 60, 10);
  minutes = minutes - hours * 60;
  return `${formatDecimal(hours, 2, 0)}:${formatDecimal(minutes, 2, 0)}`;
}

// ---------------------------------------------------------------------------------------------
export function formatDateTimeAsMvcString(d: Date) {
  return (
    "" +
    d.getFullYear() +
    "-" +
    padZeroes(d.getMonth() + 1, 2) +
    "-" +
    padZeroes(d.getDate(), 2) +
    "T" +
    padZeroes(d.getHours(), 2) +
    ":" +
    padZeroes(d.getMinutes(), 2) +
    ":00"
  );
}

// ---------------------------------------------------------------------------------------------
export function formatDecimal(num: number, digits: number, decimals: number) {
  let val = Number(num).toFixed(decimals).toString();

  let negative = "";
  if (val.indexOf("-") === 0) {
    val = val.substring(1);
    negative = "-";
  }

  const s = "0000000000" + val;
  const decPos = val.indexOf(".");
  if (decPos === -1) {
    return negative + s.substr(s.length - Math.max(val.length, digits));
  } else {
    return (
      negative +
      s.substr(s.length - Math.max(val.length, digits + decimals + 1))
    );
  }
}

// ---------------------------------------------------------------------------------------------
function padZeroes(val: any, digitCount: number) {
  const s = "0000000000" + val;
  return s.substr(s.length - digitCount);
}
