import React, { useContext, useEffect, useState } from "react";
import {
  IonButton,
  IonButtons,
  IonIcon,
  IonContent,
  IonFooter,
  IonHeader,
  IonGrid,
  IonRow,
  IonCol,
  IonToolbar,
} from "@ionic/react";
import { arrowBackOutline, bookmark, trash } from "ionicons/icons";
// import "../EditEvent/EditEventModal.scss";
import "../../global-styles.scss";
import { ApplicationUserContext } from "../../context/ApplicationUserContext";
import useDialogBox from "../../hooks/useDialogBox";
import { Message } from "../../orbital-interfaces/Message";
import { AuthorizedItemsContext } from "../../context/AuthorizedItemsContext";
import { getMessageDeliveryTimeString } from "../../util/time-helper";
import scheduleEventAPI from "../../api/ScheduleEventsAPI";
import messageAPI from "../../api/MessageAPI";
import { ScheduleEvent } from "../../orbital-interfaces/ScheduleEvent";
import EventReqeustEditor from "../EditEvent/editor/EventRequestEditor";
// import "./ViewMessageModal.scss";
import { MessagesReducerAction } from "../../pages/MessagesPage";
import useYesOrCancelDialogBox from "../../hooks/useYesOrCancelDialogBox";
import {
  getEventEditAuthorization,
  EventEditAuthorization,
} from "../EditEvent/EditEventAuthorization";
import { MessagesContext } from "../../context/MessagesContext";

interface ViewMessageModalProps {
  message: Message | null;
  onCloseModal: (msg: string | null) => void;
  updateMessages: React.Dispatch<MessagesReducerAction>;
}

// --------------------------------------------------------------------------------------
const ViewMessagetModal: React.FC<ViewMessageModalProps> = ({
  message,
  onCloseModal,
  updateMessages,
}) => {
  const dlg = useDialogBox();
  const yesCancelDlg = useYesOrCancelDialogBox();
  const { user } = React.useContext(ApplicationUserContext);
  const { auth } = React.useContext(AuthorizedItemsContext);
  const [scheduleEvent, setScheduleEvent] = useState(
    null as ScheduleEvent | null
  );
  const [eventEditAuth, setEventEditAuth] = useState(
    null as EventEditAuthorization | null
  );
  const [readCount, setReadCount] = useState(null as number | null);
  const [usageText, setUsageText] = useState(null as string | null);
  const [pinned, setPinned] = useState(false);
  const { refreshCount } = useContext(MessagesContext);

  // --------------------------------------------------------------------------------------
  useEffect(() => {
    if (!message) {
      return;
    }

    if (message.scheduleEventId) {
      scheduleEventAPI
        .getScheduleEvent(user, message.scheduleEventId)
        .then((event) => {
          if (event) {
            setScheduleEvent(event);
            const newEventEditAuth = getEventEditAuthorization(
              event.EventTypeId,
              event.EventStatusId,
              event.EmployeeId,
              event.EventStart,
              event.EventEnd,
              event.LocationId,
              event.DepartmentId,
              event.PositionId,
              event.ConfirmedBy !== null &&
              event.ConfirmedBy !== undefined &&
              event.ConfirmedBy !== "",
              auth
            );
            setEventEditAuth(newEventEditAuth);
          }
        });
    } else {
      if (scheduleEvent) {
        setScheduleEvent(null);
        setUsageText(null);
      }
    }

    messageAPI.getReadCount(user, message.messageId).then((newReadCount) => {
      setReadCount(newReadCount);
    });

    if (!message.isViewed) {
      messageAPI.setIsViewed(user, message.messageId, true).then(() => {
        updateMessages({
          type: "UPDATE-VIEWED",
          msgId: message.messageId,
          isViewed: true,
        });
      });
    }

    setPinned(message.isPinned);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [message?.messageId]);

  // --------------------------------------------------------------------------------------
  const onPinnedClick = () => {
    const newPinValue = !pinned;
    messageAPI.setIsPinned(user, message!.messageId, newPinValue).then(() => {
      setPinned(newPinValue);
      updateMessages({
        type: "UPDATE-PINNED",
        msgId: message!.messageId,
        isPinned: newPinValue,
      });
    });
  };

  // --------------------------------------------------------------------------------------
  const onDeleteClick = () => {
    yesCancelDlg.showDialog(
      "Delete this message?",
      () => {
        messageAPI.deleteMessage(user, message!.messageId).then(() => {
          updateMessages({ type: "DELETE", msgId: message!.messageId });
          onCancel("Message Deleted");
        });
      },
      () => { }
    );
  };

  // --------------------------------------------------------------------------------------
  const onCancel = (msg: string | null) => {
    if (!message?.isViewed) {
      refreshCount();
    }

    onCloseModal(msg);
  };

  if (!message || !auth || !auth.locations || auth.locations.length === 0) {
    return (
      <>
        <div>Loading</div>
        {dlg.alertBoxJsx}
      </>
    );
  } else {
    return (
      <>
        <IonHeader className="ion-no-border">
          <IonToolbar>
            <IonButtons slot="start">
              <IonButton
                onClick={() => {
                  onCancel(null);
                }}
              >
                <IonIcon icon={arrowBackOutline} mode="md" slot="icon-only" />
              </IonButton>
            </IonButtons>
            <IonButtons slot="end">
              <IonButton
                onClick={() => {
                  onPinnedClick();
                }}
              >
                <IonIcon
                  icon={bookmark}
                  style={pinned ? { color: "black" } : { color: "lightgray" }}
                />
              </IonButton>
              <IonButton
                onClick={() => {
                  onDeleteClick();
                }}
              >
                <IonIcon icon={trash} />
              </IonButton>
            </IonButtons>
          </IonToolbar>
        </IonHeader>

        <IonContent class="outer-contentMessage">
          <IonGrid>
            <IonRow class="messageIonRow-header">
              <IonCol class="messageHeaderSubject" size="12" >
                {message!.subject}
              </IonCol>

              <IonCol class="messageHeaderDetails" size="12">
                From: {message!.fromEmployeeFirstName}{" "}
                {message!.fromEmployeeLastName}
              </IonCol>

              <IonCol class="messageHeaderDetails" size="9" >
                {getMessageDeliveryTimeString(message!.deliveryDateTime)}
              </IonCol>

              <IonCol class="readCounter" size="3" >
                {readCount !== null && <span>{readCount} Read</span>}
              </IonCol>
            </IonRow>
            <div></div>
            
            <IonRow class="messageIonRow-content">
              <IonCol class="messageBlock" size="12">
                <div
                  dangerouslySetInnerHTML={{ __html: message?.messageText }}
                />
              </IonCol>
            </IonRow>

            {usageText !== null && (
              <IonRow>
                <IonCol size="12">{usageText}</IonCol>
              </IonRow>
            )}
          </IonGrid>

          {dlg.alertBoxJsx}
          {yesCancelDlg.dialogJsx}
        </IonContent>

        <IonFooter>
          {scheduleEvent !== null && (
            <EventReqeustEditor
              auth={auth}
              eventEnd={scheduleEvent.EventEnd}
              eventType={scheduleEvent.EventTypeId}
              eventStatus={scheduleEvent.EventStatusId}
              eventEmployeeId={scheduleEvent.EmployeeId}
              eventPositionId={scheduleEvent.PositionId}
              scheduleEventId={scheduleEvent.ScheduleEventId}
              locationId={scheduleEvent.LocationId}
              update={() => { }}
              cancelEditModal={onCancel}
              eventEditAuth={eventEditAuth}
              setUsageText={setUsageText}
            />
          )}
          <IonButton
            className="apply-button"
            expand="block"
            onClick={() => {
              onCancel(null);
            }}
            color="tertiary"
          >
            BACK
          </IonButton>
        </IonFooter>
      </>
    );
  }
};

export default ViewMessagetModal;
