import React, { useContext, useState } from "react";
// import "./DailyNoteCard.scss";
import "../../global-styles.scss";
import {
  AdminAuthorization,
  AuthorizedLocation,
  Role,
} from "../../orbital-interfaces/authorized-items";
import { format, parseISO } from "date-fns";
import { DailyNotes } from "../../orbital-interfaces/DailyNotes";
import { IonModal } from "@ionic/react";
import EditDailyNoteModal from "./EditDailyNoteModal";
import { AuthorizedItemsContext } from "../../context/AuthorizedItemsContext";
import authHelper from "../../util/authitems-helper";

interface EventCardProps {
  note: DailyNotes;
  location: AuthorizedLocation;
  onForceRefresh: (toastMessage: string) => void;
}

// --------------------------------------------------------------------------------------
//
// --------------------------------------------------------------------------------------
const EventCard: React.FC<EventCardProps> = ({
  note,
  onForceRefresh,
}: EventCardProps) => {
  const noteDate = parseISO(note.EventStart);
  const deptName = note.DepartmentId === -1 ? "All Depts" : note.DepartmentName;
  const [showEditModal, setShowEditModal] = useState(false);
  const { auth } = useContext(AuthorizedItemsContext);

  // --------------------------------------------------------------------------------------
  function onClose(toastMessage: string) {
    onForceRefresh(toastMessage);
    setShowEditModal(false);
  }

  // --------------------------------------------------------------------------------------
  function onClick() {
    if (auth.role === Role.Employee) {
      return;
    }

    if (
      !authHelper.isAdminAuthorizedFor(
        auth,
        note.LocationId,
        note.DepartmentId,
        AdminAuthorization.CanEditSchedules
      )
    ) {
      return false;
    }

    setShowEditModal(true);
  }

  return (
    <>
      <button className="card daily-note" onClick={onClick}>
        <div className="card-first-col">
          <div className="day-name">{format(noteDate, "EEE")} </div>
          <div className="day-number">{format(noteDate, "d")}</div>
        </div>
        <div className="card-second-col">
          <div className="first-row">
            {deptName}: {note.Role === 1 ? "Employees" : "Admins"}
            <br />
            {note.Notes}
          </div>
        </div>
      </button>

      <IonModal
        isOpen={showEditModal}
        onDidDismiss={() => setShowEditModal(false)}
      >
        <EditDailyNoteModal
          isOpen={showEditModal}
          note={note}
          onCloseModal={(toast) => onClose(toast)}
          initialDate={new Date()}
          newNoteClicked={true}
        />
      </IonModal>
    </>
  );
};

export default EventCard;
