import React, { useState } from "react";
import {
  IonCheckbox,
  IonDatetime,
  IonContent,
  IonRow,
  IonCol,
  IonButtons,
  IonButton,
  IonIcon,
  IonFooter,
  IonGrid,
  IonHeader,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import { close } from "ionicons/icons";
import "../../../global-styles.scss";
import {
  setHours,
  setMinutes,
  parseISO,
  isAfter,
  isBefore,
  addMinutes,
} from "date-fns";
import useDialogBox from "../../../hooks/useDialogBox";
import { EventBreak } from "../../../orbital-interfaces/EventBreak";

interface AddBreakModalProps {
  onCancel: () => void;
  onAdd: (
    startDate: Date,
    endDate: Date,
    isRestricted: boolean,
    isPaid: boolean
  ) => void;
  currentBreaks: EventBreak[];
  eventStart: string;
  eventEnd: string;
}

// --------------------------------------------------------------------------------------
const hasOverlap = (start: Date, end: Date, breaks: EventBreak[]) => {
  const startA = start;
  const endA = end;
  let found = false;
  breaks.map((brk) => {
    const startB = addMinutes(parseISO(brk.BreakStart), 3);
    const endB = brk.BreakEnd
      ? addMinutes(parseISO(brk.BreakEnd!), -3)
      : new Date(2200, 1, 1);
    if (startA < endB && endA > startB) {
      found = true;
    }

    return found;
  });
  return found;
};

// --------------------------------------------------------------------------------------
const AddBreakModal: React.FC<AddBreakModalProps> = ({
  onCancel,
  onAdd,
  currentBreaks,
  eventStart,
  eventEnd,
}: AddBreakModalProps) => {
  const [startDate, setStartDate] = useState(
    setMinutes(setHours(new Date(), 12), 0)
  );
  const [endDate, setEndDate] = useState(
    setMinutes(setHours(new Date(), 13), 0)
  );
  const [isRestricted, setIsRestricted] = useState(false);
  const [isPaid, setIsPaid] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [isValid, setIsValid] = useState(true);
  const alert = useDialogBox();

  const onAddClick = () => {
    if (startDate > endDate) {
      alert.showAlert(
        "Invalid Start / End",
        "Break Start cannot come after Break End.",
        () => {}
      );
    } else {
      onAdd(startDate, endDate, isRestricted, isPaid);
    }
  };

  const onStartUpdate = (newStart: string) => {
    const newStartDate = parseISO(newStart);
    setStartDate(newStartDate);
    if (!isBefore(newStartDate, endDate)) {
      setIsValid(false);
      setErrorMessage("Start must be before End");
    } else if (parseISO(eventStart) > newStartDate) {
      setIsValid(false);
      setErrorMessage("Start before Event begins");
    } else if (hasOverlap(newStartDate, endDate, currentBreaks)) {
      setIsValid(false);
      setErrorMessage("Pre-existing break overlaps");
    } else {
      setIsValid(true);
      setErrorMessage("");
    }
  };

  const onEndUpdate = (newEnd: string) => {
    const newEndDate = parseISO(newEnd);
    setEndDate(newEndDate);

    if (!isAfter(newEndDate, startDate)) {
      setIsValid(false);
      setErrorMessage("End must be after Start");
    } else if (newEndDate > parseISO(eventEnd)) {
      setIsValid(false);
      setErrorMessage("End after Event ends");
    } else if (hasOverlap(startDate, newEndDate, currentBreaks)) {
      setIsValid(false);
      setErrorMessage("Pre-existing break overlaps");
    } else {
      setIsValid(true);
      setErrorMessage("");
    }
  };

  return (
    <>
      <IonHeader mode="ios">
        <IonToolbar>
          <IonTitle>Add Break</IonTitle>
          <IonButtons slot="end">
            <IonButton onClick={onCancel}>
              <IonIcon icon={close} mode="ios" slot="icon-only" />
            </IonButton>
          </IonButtons>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <IonGrid>
          <IonRow>
            <IonCol className="label" size="3">
              Start
            </IonCol>
            <IonCol size="9">
              <IonDatetime
                className="select"
                displayFormat="h:mm a"
                pickerFormat="h:mm A"
                min="2005-01-01"
                max="2030-01-01"
                minuteValues="0,5,10,15,20,25,30,35,40,45,50,55"
                value={startDate.toISOString()}
                onIonChange={(e) => onStartUpdate(e.detail.value!)}
              />
            </IonCol>
          </IonRow>

          <IonRow>
            <IonCol className="label" size="3">
              End
            </IonCol>
            <IonCol size="9">
              <IonDatetime
                className="select"
                displayFormat="h:mm a"
                pickerFormat="h:mm A"
                min="2005-01-01"
                max="2030-01-01"
                minuteValues="0,5,10,15,20,25,30,35,40,45,50,55"
                value={endDate.toISOString()}
                onIonChange={(e) => onEndUpdate(e.detail.value!)}
              />
            </IonCol>
          </IonRow>

          <IonRow>
            <IonCol size="3">
              <IonCheckbox
                className="checkbox"
                checked={isRestricted}
                onIonChange={(e) => {
                  setIsRestricted(e.detail.checked);
                }}
              />
            </IonCol>
            <IonCol className="label" size="9">
              Restricted Break
            </IonCol>
          </IonRow>

          <IonRow>
            <IonCol size="3">
              <IonCheckbox
                className="checkbox"
                onIonChange={(e) => {
                  setIsPaid(e.detail.checked);
                }}
              />
            </IonCol>
            <IonCol className="label" size="9">
              Paid Break
            </IonCol>
          </IonRow>

          <IonRow>
            <IonCol size="12" className="error-message">
              {errorMessage}
            </IonCol>
          </IonRow>
        </IonGrid>
      </IonContent>
      <IonFooter>
        <IonButton
          expand="block"
          color="secondary"
          onClick={onAddClick}
          disabled={!isValid}
        >
          ADD
        </IonButton>
      </IonFooter>
      {alert.alertBoxJsx}
    </>
  );
};

export default AddBreakModal;
