import React, { useEffect, useState } from "react";
import { IonLabel, IonCheckbox, IonCol, IonIcon } from "@ionic/react";
import "./EventStartEndEditor";
import { AuthorizedItems } from "../../../orbital-interfaces/authorized-items";
import { EditorValues, EditorReducerAction } from "../EditEventModalState";
import EventType from "../../../enums/EventType";
import EventStatus from "../../../enums/EventStatus";
import { checkmark } from "ionicons/icons";

interface EventConfirmEditorProps {
  auth: AuthorizedItems;
  editorValues: EditorValues;
  update: React.Dispatch<EditorReducerAction>;
}

// --------------------------------------------------------------------------------------
const EventConfirmEditor: React.FC<EventConfirmEditorProps> = ({
  auth,
  editorValues,
  update,
}: EventConfirmEditorProps) => {
  const [isConfirmed, setIsConfirmed] = useState(false);
  const [showConfirmed, setShowConfirmed] = useState(false);

  useEffect(() => {
    if (editorValues.scheduleEventId === -1) {
      return;
    }

    if (auth && auth.locations && auth.locations.length > 0) {
      setIsConfirmed(editorValues.isConfirmed);
    }

    // eslint-disable-next-line
  }, [
    auth,
    editorValues.eventStart,
    editorValues.eventEnd,
    editorValues.isClockedIn,
    editorValues.scheduleEventId,
  ]);

  useEffect(() => {
    if (editorValues.scheduleEventId === -1) {
      return;
    }

    const isConfirmable =
      (editorValues.eventType === EventType.Shift &&
        editorValues.eventStatus === EventStatus.Approved) ||
      (editorValues.eventType === EventType.Cover &&
        (editorValues.eventStatus === EventStatus.Request ||
          editorValues.eventStatus === EventStatus.RequestDenied));
    setShowConfirmed(isConfirmable);
  }, [
    editorValues.eventType,
    editorValues.eventStatus,
    editorValues.scheduleEventId,
  ]);

  const onCheckChange = (checked: boolean) => {
    if (checked === isConfirmed || !showConfirmed) {
      return;
    }

    setIsConfirmed(checked);
    update({
      type: "CONFIRM-UPDATED",
      isConfirmed: checked,
    });
  };

  if (!showConfirmed) {
    return (
      <>
        {/* <IonCol size="0">
          <span />
        </IonCol>
        <IonCol size="0">
          <span />
        </IonCol> */}
      </>
    );
  }

  return (
    <>
      <IonCol size="3">
        <IonLabel className="label">

          Confirm</IonLabel>
      </IonCol>

      <IonCol size="3">
        <IonCheckbox
          checked={isConfirmed}
          onIonChange={(e) => {
            onCheckChange(e.detail.checked);
          }}
        />
        <IonIcon
          className="icon-right-of-checkbox"
          icon={checkmark}
        />
      </IonCol>
    </>
  );
};

export default EventConfirmEditor;
