import React, {useContext} from "react";
import {
  IonTabs,
  IonRouterOutlet,
  IonTabBar,
  IonTabButton,
  IonIcon,
  IonLabel,
} from "@ionic/react";
import { Route, Redirect } from "react-router";
import { calendarOutline, timeOutline } from "ionicons/icons";
import SchedulePage from "./SchedulePage";
import ClockPage from "./ClockPage";
import MessagesPage from "./MessagesPage";
import MessagesTabButton from "./MessagesTabButton";
// import "./MainTabs.scss";
import "../global-styles.scss";
import {AuthorizedItemsContext} from "../context/AuthorizedItemsContext";

interface MainTabsProps {}

const MainTabs: React.FC<MainTabsProps> = () => {
  const {auth} = useContext(AuthorizedItemsContext);

  return (
    <IonTabs>
      <IonRouterOutlet>
        <Redirect exact={true} path="/tabs" to="/tabs/schedule" />

        <Route
          path="/tabs/schedule/:scheduleEventId"
          render={(routeProps) => <SchedulePage {...routeProps} />}
        />

        <Route
          path="/tabs/schedule"
          render={(routeProps) => <SchedulePage {...routeProps} />}
          exact={true}
        />

        <Route
          path="/tabs/messages/:messageId"
          render={(routeProps) => <MessagesPage {...routeProps} />}
        />

        <Route
          path="/tabs/messages"
          render={(routeProps) => <MessagesPage {...routeProps} />}
          exact={true}
        />

        <Route  path="/tabs/clock" render={() => <ClockPage />} exact={true} />
      </IonRouterOutlet>
      <IonTabBar slot="bottom">
        <IonTabButton key="1" tab="schedule" href="/tabs/schedule">
          <IonIcon icon={calendarOutline} />
          <IonLabel>Schedule</IonLabel>
        </IonTabButton>
        <IonTabButton hidden={!auth.timeClock} key="2" tab="clock" href="/tabs/clock">
          <IonIcon icon={timeOutline} />
          <IonLabel>Clock</IonLabel>
        </IonTabButton>
        <IonTabButton key="3" tab="map" href="/tabs/messages">
          <MessagesTabButton />
        </IonTabButton>
      </IonTabBar>
    </IonTabs>
  );
};

export default MainTabs;
