import React, { CSSProperties, useContext } from "react";
import {
  getDurationStringFromTimeSpan,
  getEventInformationStartEnd,
} from "../../util/time-helper";
// import "./EventCard.scss";
import "../../global-styles.scss";
import { AuthorizedLocation } from "../../orbital-interfaces/authorized-items";
import { format, parseISO } from "date-fns";
import { EventInformation } from "../../orbital-interfaces/TEC";
import { time } from "ionicons/icons";
import { IonIcon } from "@ionic/react";
import {
  getEventEditAuthorization,
  EventEditAuthorization,
} from "../EditEvent/EditEventAuthorization";
import { AuthorizedItemsContext } from "../../context/AuthorizedItemsContext";
import EventType from "../../enums/EventType";

interface EventCardProps {
  event: EventInformation;
  location: AuthorizedLocation;
  multiLocation: boolean;
  showEmployeeName: boolean;
  onEdit: (ev: EventInformation, eventEditAuth: EventEditAuthorization) => void;
}

const colorMap: any = {
  1: "#9CBA6A",
  2: "#BFBFBF",
  3: "#FFAD00",
  4: "#C41A26",
  6: "#677A46",
  7: "#FFAD00",
};

const statusTimeColor: any = {
  1: { color: "black" },
  2: { color: "#C41A26" },
  4: { color: "#BCBCBC" },
};

const EventCard: React.FC<EventCardProps> = ({
  location,
  event,
  multiLocation,
  showEmployeeName,
  onEdit,
}: EventCardProps) => {
  const { auth } = useContext(AuthorizedItemsContext);
  const displayNameStyle: CSSProperties = showEmployeeName
    ? { display: "inline-block" }
    : { display: "none" };

  const eventStartDate = parseISO(event.EventStart);
  const multiDepartment = multiLocation
    ? true
    : location.departments.filter((d) =>
        d.positions.filter((p) => p.hasPosition)
      ).length > 1;

  let posDisplay = "";
  if (multiLocation && event.AbbreviatedLocationName) {
    posDisplay = event.AbbreviatedLocationName + "/";
  }

  if (multiDepartment && event.AbbreviatedDepartmentName) {
    posDisplay += event.AbbreviatedDepartmentName + "/";
  }

  posDisplay += event.PositionName;

  const eventTypeBorder = {
    borderLeft: `7px solid ${colorMap[event.EventTypeId]} `,
  };

  const timeColor: CSSProperties = statusTimeColor[event.EventStatusId];

  const onUserClick = () => {
    const eventEditAuth = getEventEditAuthorization(
      event.EventTypeId,
      event.EventStatusId,
      event.EmployeeId,
      event.EventStart,
      event.EventEnd,
      event.LocationId,
      event.DepartmentId!,
      event.PositionId,
      event.IsConfirmed,
      auth
    );
    if (eventEditAuth === null) {
      return;
    }

    onEdit(event, eventEditAuth);
  };

  return (
    <button
      className="card"
      onClick={() => {
        onUserClick();
      }}
    >
      <div className="card-first-col">
        <div className="day-name">{format(eventStartDate, "EEE")} </div>
        <div className="day-number">{format(eventStartDate, "d")}</div>
      </div>
      <div className="card-second-col" style={eventTypeBorder}>
        <div className="time-row">
          {event.IsClockedIn && <IonIcon icon={time} />}
          <span style={timeColor}>
            {getEventInformationStartEnd(event)}&nbsp;&nbsp;
          </span>
          {getDurationStringFromTimeSpan(
            event.EventTypeId === EventType.Absent
              ? event.Time
              : event.EventDuration
          )}
        </div>
        <div className="name-row" style={displayNameStyle}>
          {event.FirstName} {event.LastName}
        </div>
        <div className="dept-position-row">
          {posDisplay}&nbsp;&nbsp;
          {event.Tag && `~${event.Tag}`}
        </div>
      </div>
    </button>
  );
};

export default EventCard;
