import React, {useState, useEffect, useRef} from "react";
import {
  IonLabel,
  IonSelect,
  IonRow,
  IonCol,
  IonSelectOption,
} from "@ionic/react";
import "../../../global-styles.scss";
import "./EventPositionEditor";
import {
  AuthorizedItems,
  AuthorizedLocation,
} from "../../../orbital-interfaces/authorized-items";
import { EditorValues, EditorReducerAction } from "../EditEventModalState";
import authHelper from "../../../util/authitems-helper";

interface EventPositionEditorProps {
  auth: AuthorizedItems;
  editorValues: EditorValues;
  update: React.Dispatch<EditorReducerAction>;
}

interface Option {
  id: number;
  name: string;
}

// --------------------------------------------------------------------------------------
const createPositionList = (loc: AuthorizedLocation[]): Option[] => {
  const isMultiLoc = loc.length > 1;
  return loc.flatMap((l) =>
    l.departments.flatMap((d) =>
      d.positions.map((p) => {
        const isMultiDep = l.departments.length > 1;
        let name = "";
        if (isMultiLoc) {
          name += (l.abbr ? l.abbr : l.name) + "/";
        }

        if (isMultiDep) {
          name += (d.abbr ? d.abbr : d.name) + "/";
        }

        name += p.name;
        return { id: p.id, name: name };
      })
    )
  );
};

// --------------------------------------------------------------------------------------
const EventPositionEditor: React.FC<EventPositionEditorProps> = ({
  auth,
  editorValues,
  update,
}: EventPositionEditorProps) => {
  const [positionList, setPositionList] = useState([] as Option[]);
  const posIdRef = useRef(null as number | null);

  useEffect(() => {
    if (editorValues.scheduleEventId === -1) {
      return;
    }

    if (auth && auth.locations && auth.locations.length > 0) {
      setPositionList(createPositionList(auth.locations));
    }
  }, [auth, editorValues.scheduleEventId]);

  const updatePosition = (posId: number) => {
    if (posId === posIdRef.current) {
      return;
    }

    if (posId === editorValues.position.position.id) {
      return;
    }

    const selected = authHelper.getSelectedPosition(auth.locations, posId);
    update({ type: "POSITION-UPDATED", selectedPosition: selected });
  };

  return (
    <IonRow className="edit-row">
      <IonCol size="3">
        <IonLabel className="label">Position</IonLabel>
      </IonCol>
      <IonCol size="9">
        <IonSelect
          mode="ios"
          className="select"
          interface="action-sheet"
          disabled={editorValues.isReadonly}
          value={editorValues.position.position.id}
          onIonChange={(e) => updatePosition(e.detail.value)}
        >
          {positionList.map((p) => (
            <IonSelectOption key={p.id} value={p.id}>
              {p.name}
            </IonSelectOption>
          ))}
        </IonSelect>
      </IonCol>
    </IonRow>
  );
};

export default EventPositionEditor;
