import React from "react";
// import "./EventCard.scss";
import "../../global-styles.scss";
import Skeleton from "react-loading-skeleton";

interface EventCardSkeletonProps {
  threeRows?: boolean;
}

const EventCardSkeleton: React.FC<EventCardSkeletonProps> = ({
  threeRows,
}: EventCardSkeletonProps) => {
  return (
    <button className="card">
      <div className="card-first-col">
        <div className="day-name">
          <Skeleton />{" "}
        </div>
        <div className="day-number">
          <Skeleton />
        </div>
      </div>
      <div style={{ padding: "3px" }}>
        <Skeleton width={5} height={45} />
      </div>
      <div className="card-second-col">
        <div className="time-row">
          <span>
            <Skeleton width={150} height={9} />
            &nbsp;&nbsp;
          </span>
          <Skeleton />
        </div>
        {threeRows && (
          <div>
            <Skeleton width={80} height={10} />
          </div>
        )}
        <div className="dept-position-row">
          <Skeleton width={150} height={10} />
          &nbsp;&nbsp;
        </div>
      </div>
    </button>
  );
};

export default EventCardSkeleton;
