import EventType from "../../enums/EventType";
import EventStatus from "../../enums/EventStatus";
import {
  AdminAuthorization,
  AuthorizedItems,
  Role,
} from "../../orbital-interfaces/authorized-items";
import { isAfter, parseISO } from "date-fns";
import authHelper from "../../util/authitems-helper";

export enum EventEditEnum {
  None,
  UndoCoverRequest,
  CreateShiftRequest,
  DeleteShiftRequest,
  CreateCoverRequest,
  Delete,
}

export interface Option {
  value: number;
  text: string;
}

export interface EventEditAuthorization {
  buttonText: string;
  enableConfirm: boolean;
  isReadonly: boolean;
  text: string;
  usage: EventEditEnum;
}

export const getEventNewAuthorization = (
  auth: AuthorizedItems
): EventEditAuthorization | null => {

  // Unsure why eslint is not detecting that location, department,
  // and position are actually being used, here; disabling for that
  // reason.

  // eslint-disable-next-line
  let location = auth.locations[0];
  // eslint-disable-next-line
  let dept = auth.locations[0].departments[0];
  // eslint-disable-next-line
  let position = auth.locations[0].departments[0].positions[0];
  let hasPositionFound = false;
  for (let i = 0; i < auth.locations.length && !hasPositionFound; i++) {
    for (
      let j = 0;
      j < auth.locations[i].departments.length && !hasPositionFound;
      j++
    ) {
      for (
        let k = 0;
        k < auth.locations[i].departments[j].positions.length &&
        !hasPositionFound;
        k++
      ) {
        if (auth.locations[i].departments[j].positions[k].hasPosition) {
          location = auth.locations[i];
          dept = auth.locations[i].departments[j];
          position = auth.locations[i].departments[j].positions[k];
          hasPositionFound = true;
        }
      }
    }
  }

  return {
    buttonText: "",
    enableConfirm: true,
    isReadonly: false,
    text: "",
    usage: EventEditEnum.None,
  } as EventEditAuthorization;
};

// --------------------------------------------------------------------------------------
export const getEventEditAuthorization = (
  eventType: EventType,
  eventStatus: EventStatus,
  eventEmployeeId: number,
  eventStart: string,
  eventEnd: string,
  eventLocationId: number,
  eventDepartmentId: number,
  eventPositionId: number,
  eventConfirmed: boolean,
  auth: AuthorizedItems
): EventEditAuthorization | null => {

  const retVal = {
    buttonText: "",
    enableConfirm: true,
    isReadonly: false,
    text: "",
    usage: EventEditEnum.None,
  } as EventEditAuthorization;

  if (auth.role === Role.BusinessAdmin) {
    return retVal;
  }

  if (auth.role === Role.LocationAdmin) {
    if (authHelper.isPositionSpecificEvent(eventType)) {
      if (auth.locations.filter((l) => l.id === eventLocationId).length === 0) {
        return null;
      }
    }

    return retVal;
  }

  const endOfEvent = parseISO(eventEnd);
  const now = new Date();
  const afterEnd = isAfter(now, endOfEvent);
  let employeeEvent = false;
  const adminAuth = authHelper.getAdminAuthorizationForDepartment(
    auth,
    eventDepartmentId
  );

  // If the user is in the Admin role
  if (auth.role === Role.Admin) {
    if (eventEmployeeId === auth.employeeId) {
      if (eventType === EventType.Clock) {
        if (
          (adminAuth & AdminAuthorization.CanEditOwnClockEvents) ===
          AdminAuthorization.CanEditOwnClockEvents
        ) {
          return retVal;
        } else {
          return null;
        }
      }

      if (
        (adminAuth & AdminAuthorization.CanEditSchedules) ===
        AdminAuthorization.CanEditSchedules
      ) {
        return retVal;
      }

      employeeEvent = true;
    } else {
      // Not the user's event, someone else's

      if (eventType === EventType.Clock) {
        if (
          (adminAuth & AdminAuthorization.CanEditEmployeeClockEvents) ===
          AdminAuthorization.CanEditEmployeeClockEvents
        ) {
          return retVal;
        } else {
          return null;
        }
      }

      if (
        authHelper.canEditSchedules(auth, eventLocationId, eventDepartmentId)
      ) {
        return retVal;
      } else {
        // If the admin has a position in the selected department
        if (authHelper.adminIsInDepartment(auth, eventDepartmentId)) {
          // Treat the admin as an employee for event editing
          employeeEvent = true;
        } else {
          // disable editing
          return null;
        }
      }
    }
  }

  if (auth.role === Role.Employee || employeeEvent) {
    if (eventEmployeeId === auth.employeeId) {
      if (afterEnd) {
        return null;
      }

      if (eventType === EventType.Clock) {
        return null;
      }

      if (eventType === EventType.Shift) {
        if (eventStatus === EventStatus.Approved) {
          return {
            ...retVal,
            buttonText: "Create Cover-Request",
            isReadonly: true,
            usage: EventEditEnum.CreateCoverRequest,
          };
        }

        if (eventStatus === EventStatus.Request) {
          return {
            ...retVal,
            buttonText: "Undo Shift-Request",
            enableConfirm: false,
            isReadonly: true,
            text: "Click OK to remove your Shift-Request",
            usage: EventEditEnum.DeleteShiftRequest,
          };
        }

        // else Shift Request-Denied
        return null;
      }

      if (eventType === EventType.Cover) {
        if (eventStatus === EventStatus.Request) {
          return {
            ...retVal,
            buttonText: "Undo Cover-Request",
            enableConfirm: true,
            isReadonly: true,
            usage: EventEditEnum.UndoCoverRequest,
          };
        }

        return null; // Cover Request-Denied
      }

      if (
        !authHelper.isPositionSpecificEvent(eventType) &&
        authHelper.canEditNonPositionEvents(auth, eventDepartmentId)
      ) {
        return {
          ...retVal,
          buttonText: "Delete Request",
          enableConfirm: false,
          isReadonly: true,
          text: "Click OK to delete this event",
          usage: EventEditEnum.Delete,
        };
      }

      return null;
    }

    // This is someone else's event being viewed by an Employee
    if (eventType === EventType.Cover && eventStatus === EventStatus.Request) {
      return {
        ...retVal,
        buttonText: "Make Shift-Request",
        enableConfirm: false,
        isReadonly: true,
        text: "",
        usage: EventEditEnum.CreateShiftRequest,
      };
    }

    return null;
  }

  return null;
};

