import React from "react";
import { OrbitalTime } from "../../orbital-interfaces/OrbitalTime";
import { LocationSettings } from "../../orbital-interfaces/authorized-items";
import { getTimeStringFromOrbitalTime } from "../../util/time-helper";
interface PayrollDetailListProps {
  time: OrbitalTime | null;
  locSettings: LocationSettings;
  fontColor: string;
}

function getHoursString(locsettings: LocationSettings, time: OrbitalTime) {
  return getTimeStringFromOrbitalTime(locsettings, time);
}

// --------------------------------------------------------------------------------------
const PayrollOrbitalTimeItem: React.FC<PayrollDetailListProps> = ({
  time,
  locSettings,
  fontColor,
}: PayrollDetailListProps) => {
  if (time === null) {
    return <span> -- </span>;
  }

  const displayColor = time.ActualHours === 0.0 ? "#ddd" : fontColor;

  return (
    <span style={{ color: displayColor }}>
      {getHoursString(locSettings, time)}
    </span>
  );
};

export default PayrollOrbitalTimeItem;
