import { ApplicationUser } from "../context/ApplicationUserContext";
import apiHelper from "./OrbitalApiHelper";
import { formatDateTimeAsMvcString } from "../util/time-helper";
import { OrbitalTime } from "../orbital-interfaces/OrbitalTime";

export interface PayrollPeriodHoursPerAbsent {
  absentId: number;
  time: OrbitalTime;
}

export interface PayrollPeriodDateItem {
  date: string;
  regular: OrbitalTime;
  dailyOvertime: OrbitalTime;
  weeklyOvertime: OrbitalTime;
  special: OrbitalTime;
  absent: OrbitalTime;
  timePerAbsent: PayrollPeriodHoursPerAbsent[];
}

export interface PayrollPeriodData {
  start: string;
  end: string;
  list: PayrollPeriodDateItem[];
  error: boolean;
  message: string;
  totalRegular: OrbitalTime;
  totalWeeklyOvertime: OrbitalTime;
  totalDailyOvertime: OrbitalTime;
  totalAbsent: OrbitalTime;
  totalSpecial: OrbitalTime;
}

// ----------------------------------------------------------------------------------------------
const getPayPeriodDateForEmployee = async (
  user: ApplicationUser,
  locationId: number,
  date: Date
) => {
  const dateStr = formatDateTimeAsMvcString(date);
  return (await apiHelper.get(
    user,
    "api/v2/payroll/get/" + locationId + "/" + dateStr
  )) as PayrollPeriodData;
};

export default {
  getPayPeriodDateForEmployee,
};
