import React, {useContext, useEffect, useRef, useState} from "react";
import {IonCol, IonLabel, IonRow, IonSelect, IonSelectOption,} from "@ionic/react";
import "../../../global-styles.scss";
import "./EventEmployeeEditor";
import {
  AdminAuthorization,
  AuthorizedItems,
  Role} from "../../../orbital-interfaces/authorized-items";
import employeeAPI from "../../../api/EmployeesAPI";
import {ApplicationUserContext} from "../../../context/ApplicationUserContext";
import {parseISO} from "date-fns";
import {EditorReducerAction, EditorValues} from "../EditEventModalState";
import {IdName} from "../../../orbital-interfaces/IdName";
import {useShowLoading} from "../../../hooks/useShowLoading";
import {log} from "../../../util/logger";
import authHelper from "../../../util/authitems-helper";

interface EventEmployeeEditorProps {
  auth: AuthorizedItems;
  editorValues: EditorValues;
  update: React.Dispatch<EditorReducerAction>;
}

// --------------------------------------------------------------------------------------
const EventEmployeeEditor: React.FC<EventEmployeeEditorProps> = ({
  auth,
  editorValues,
  update,
}: EventEmployeeEditorProps) => {
  const [employeeList, setEmployeeList] = useState([] as IdName[]);
  const loading = useShowLoading("Please wait...");
  const { user } = useContext(ApplicationUserContext);
  const empIdRef = useRef(null as number | null);

  // --------------------------------------------------------------------------------------
  useEffect(() => {
    if (user && user.isLoggedin && editorValues.scheduleEventId !== -1) {
      log("edit_event_get_employeeList", { user: user.username});
      loading.setShowLoading(true);
      getEmployees(editorValues);
    }
    // eslint-disable-next-line
  }, [user, editorValues.position.position.id, editorValues.scheduleEventId, editorValues.employee.id]);

  // --------------------------------------------------------------------------------------
  const onChange = (employeeId: number) => {
    if (employeeId === -5 || employeeId === editorValues.employee.id) {
      return;
    }

    if (employeeId === empIdRef.current) {
      return;
    }

    empIdRef.current = employeeId;
    const emp = employeeList.filter((e) => e.id === employeeId)[0];
    if (employeeId === -1) {
      update({
        type: "EMPLOYEE-UPDATED",
        employee: { id: -1, name: "SELECT EMPLOYEE" },
      });
    } else {
      update({
        type: "EMPLOYEE-UPDATED",
        employee: { id: employeeId, name: emp.name },
      });
    }
  };

  // --------------------------------------------------------------------------------------
  const getEmployees = (ev: EditorValues) => {
    const justEmployee = [{ id: auth.employeeId, name: auth.fullName}];

    if (auth.role === Role.Employee) {
      setEmployeeList(justEmployee);
      loading.setShowLoading(false);
      return;
    }

    if (auth.role === Role.Admin) {
      const dept = authHelper.getDepartmentFromPositionId(auth, ev.position.position.id);
      if (dept === null) {
        loading.setShowLoading(false);
        setEmployeeList(justEmployee);
        return;
      }

      if (!authHelper.isAdminAuthorizedFor(
        auth,
        ev.locationId,
        dept.id,
        AdminAuthorization.CanEditSchedules)) {
        setEmployeeList(justEmployee);
        loading.setShowLoading(false);
        return;
      }

    }

    employeeAPI
      .getEmployeesByLocationIdPositionIdAndActiveDateRange(
        user,
        ev.position.location.id,
        ev.position.position.id,
        parseISO(ev.eventStart),
        parseISO(ev.eventEnd),
        ev.position.location.settings.EmployeeSort
      )
      .then((employees) => {
        setEmployeeList(employees);
        const empIds = employees.map((e) => e.id);

        if (!empIds.includes(ev.employee.id))
        {
          update({
            type: "EMPLOYEE-UPDATED",
            employee: { id: -1, name: "SELECT EMPLOYEE" }
          });
        }

        loading.setShowLoading(false);
      });
  };

  if (employeeList.length === 0) {
    return (
      <>
        <IonRow className="edit-row">
          <IonCol size="3">
            <IonLabel className="label">Employee</IonLabel>
          </IonCol>
          <IonCol size="9">
            <IonSelect
            mode="ios"
            className="select"
            interface="action-sheet"
            value={-5}
            >
              <IonSelectOption key="-5" value="-5">
                {editorValues.employee.name}
              </IonSelectOption>
            </IonSelect>
          </IonCol>
        </IonRow>
        {loading.showLoadingJsx}
      </>
    );
  }

  return (
    <>
      <IonRow className="edit-row">
        <IonCol size="3">
          <IonLabel className="label">Employee</IonLabel>
        </IonCol>
        <IonCol size="9">
          <IonSelect
            mode="ios"
            className="select"
            interface="action-sheet"
            disabled={editorValues.isReadonly || auth.role === Role.Employee}
            value={editorValues.employee.id}
            onIonChange={(e) => {
              onChange(e.detail.value);
            }}
          >
            {employeeList.length > 1 && (
                <IonSelectOption key={-1} value={-1}>
                  Select Employee
                </IonSelectOption>
            )}
            {employeeList.map((emp) => (
              <IonSelectOption key={emp.id} value={emp.id}>
                {emp.name}
              </IonSelectOption>
            ))}
          </IonSelect>
        </IonCol>
      </IonRow>
      {loading.showLoadingJsx}
    </>
  );
};

export default EventEmployeeEditor;
