import React, { useEffect, useState } from "react";
// import "./ClockPanel.scss";
import "../../global-styles.scss";
import { ApplicationUserContext } from "../../context/ApplicationUserContext";
import { AuthorizedItemsContext } from "../../context/AuthorizedItemsContext";
import authHelper from "../../util/authitems-helper";
import Map from "./Map";
import useYesOrCancelDialogBox from "../../hooks/useYesOrCancelDialogBox";
import useSimpleToast from "../../hooks/useSimpleToast";
import {Capacitor, Geolocation, GeolocationPosition} from "@capacitor/core";
import env, {getConstants} from "../../api/environment";

interface GeoLocationComponentProps {
  updateCallback: (lat?: number, lng?: number) => void;
  isShowing: boolean;
}

// Possible values for code, https://developer.mozilla.org/en-US/docs/Web/API/GeolocationPositionError
// 1: PERMISSION_DENIED
// 2: POSITION_UNAVAILABLE
// 3: TIMEOUT
interface GeolocationPositionError {
  code: number;
}

const geoPositionOptions = {
  enableHighAccuracy: true,
  maximumAge: 1000,
};


// ------------------------------------------------------------------------------------
function isGoogleMapsLoaded(): boolean {
  const loaded = !(
    google === undefined ||
    google.maps === undefined ||
    google.maps.geometry === undefined ||
    google.maps.geometry.spherical === undefined ||
    google.maps.geometry.spherical.computeDistanceBetween === undefined ||
    google.maps.LatLng === undefined
  );

  if (!loaded) {
    console.log("WARNING: google maps not yet loaded");
  }

  return loaded;
}

// ------------------------------------------------------------------------------------
//
// ------------------------------------------------------------------------------------
const GeoLocationComponent: React.FC<GeoLocationComponentProps> = ({
  updateCallback,
  isShowing
}: GeoLocationComponentProps) => {
  const { user } = React.useContext(ApplicationUserContext);
  const { auth } = React.useContext(AuthorizedItemsContext);
  const [watchPositionId, setWatchPositionId] = useState("" as string | number);
  const [currentPosition, setCurrentPosition] = useState(null as GeolocationPosition | Position | null);
  const yesCancelDlg = useYesOrCancelDialogBox();
  const toast = useSimpleToast();

  // ------------------------------------------------------------------------------------
  useEffect(() => {

    // ----------------------------------------------------------------------------------------------
    function startWatchPosition(
      callback: (lat?: number, lng?: number) => void
    ): string | number {

      const watchPositionCallBack = (position: GeolocationPosition | Position) => {
        if (!position || !position.coords) {
          return;
        }

        let positionChanged = false;
        if (currentPosition === null) {
          positionChanged = true;
        } else {
          if (isGoogleMapsLoaded())
          {
            const oldPos = new google.maps.LatLng(
              currentPosition.coords.latitude,
              currentPosition.coords.longitude
            );

            const newPos = new google.maps.LatLng(
              position.coords.latitude,
              position.coords.longitude
            );

            const distance =
              google.maps.geometry.spherical.computeDistanceBetween(
                oldPos,
                newPos
              ) * 3.28084;

            const minChange = getConstants(user).minimumFeetForGeolocationUpdate;
            positionChanged = distance > minChange;
          } else {
            positionChanged = true;
          }
        }

        if (positionChanged)
        {
          console.log("GEO: position changed");
          setCurrentPosition(position);
          callback(position.coords.latitude, position.coords.longitude);
        }
      };

      const errCallback = (err: GeolocationPositionError) => {
        console.log("GEO: Could not get geolocation, error code " + err.code);
        if (err.code === 1) {
          // permission denied
          callback();
        }
      };

      let watchId = "" as string | number;
      if (
        env.settings.hasCapacitor &&
        Capacitor.isPluginAvailable("Geolocation")
      ) {
        console.log("GEO: Using Cap plugin to watch position");
        try {
          watchId = Geolocation.watchPosition(
            geoPositionOptions,
            watchPositionCallBack
          );

          Geolocation.getCurrentPosition(geoPositionOptions).then(pos => {
            watchPositionCallBack(pos);
          });
        } catch (err) {
          errCallback({ code: 1 });
        }
      } else if (navigator && navigator.geolocation) {
        console.log("GEO: Using browser API");
        watchId = navigator.geolocation.watchPosition(
          // @ts-ignore  (watchPositionCallBack's GeolocationPosition param is wonky between Cap & Nav
          watchPositionCallBack,
          errCallback,
          geoPositionOptions
        );

        navigator.geolocation.getCurrentPosition((pos) => {
          watchPositionCallBack(pos);
        });
      } else {
        console.log(
          "GEO: Location unavailable via cap/nav"
        );
        return "";
      }

      console.log("GEO: start watching, watchId " + watchId);
      return watchId;
    }

    // ----------------------------------------------------------------------------------------------
    function clearWatchPosition(watchId: string | number) {
      if (watchId === "") {
        return;
      }

      if (
        env.settings.hasCapacitor &&
        Capacitor.isPluginAvailable("Geolocation")
      ) {
        Geolocation.clearWatch({ id: watchId as string }).then(() => {
          console.log(`GEO: cleared watchId ${watchId} from Capacitor plugin`);
        });
      } else if (navigator && navigator.geolocation) {
        navigator.geolocation.clearWatch(watchId as number);
        console.log(`GEO: cleared watchId ${watchId} from browser API`);
      }
    }

    if (authHelper.requiresGeolocation(auth.locations)) {
      if (isShowing) {
        setWatchPositionId(startWatchPosition(updateCallback));
      } else {
        clearWatchPosition(watchPositionId);
        setCurrentPosition(null);
        setWatchPositionId("");
      }
    }
    // eslint-disable-next-line
  }, [
    isShowing, user.isLoggedin
  ]);

  return (
    <>
      {auth &&
      auth.locations &&
      auth.locations.length > 0 &&
      authHelper.requiresGeolocation(auth.locations) && <Map pos={currentPosition} />}
      {yesCancelDlg.dialogJsx}
      {toast.simpleToastJsx}
    </>
  );



};

export default GeoLocationComponent;
