import React, { useContext, useState } from "react";
import { IonButton, IonModal } from "@ionic/react";
// import "./EventRequestEditor.scss";
import "../../../global-styles.scss";
import { AuthorizedItems } from "../../../orbital-interfaces/authorized-items";
import { EditorReducerAction } from "../EditEventModalState";
import EventRequestModal from "./EventRequestModal";
import authHelper from "../../../util/authitems-helper";
import reqApi from "../../../api/RequestsAPI";
import { ApplicationUserContext } from "../../../context/ApplicationUserContext";
import EventType from "../../../enums/EventType";
import EventStatus from "../../../enums/EventStatus";
import useDialogBox from "../../../hooks/useDialogBox";
import {
  EventEditAuthorization,
  EventEditEnum,
} from "../EditEventAuthorization";

interface EventRequestEditorProps {
  auth: AuthorizedItems;
  update: React.Dispatch<EditorReducerAction>;
  cancelEditModal: (message: string) => void;
  setUsageText: (usageText: string) => void;
  eventEnd: string;
  eventType: EventType;
  eventStatus: EventStatus;
  eventEmployeeId: number;
  eventPositionId: number;
  scheduleEventId: number;
  locationId: number;
  eventEditAuth: EventEditAuthorization | null;
}

// --------------------------------------------------------------------------------------
const EventRequestEditor: React.FC<EventRequestEditorProps> = ({
  auth,
  cancelEditModal,
  eventType,
  eventStatus,
  scheduleEventId,
  locationId,
  eventEditAuth,
}: EventRequestEditorProps) => {
  const { user } = useContext(ApplicationUserContext);
  const [showRequestModal, setShowRequestModal] = useState(false);
  const dlg = useDialogBox();

  const onComplete = (message: string) => {
    setShowRequestModal(false);
    cancelEditModal(message);
  };

  const onApply = (
    employeeId: number | null,
    notes: string,
    sendMessage: boolean
  ) => {
    switch (eventEditAuth!.usage) {
      case EventEditEnum.CreateCoverRequest:
        reqApi
          .createCoverRequest(
            user,
            scheduleEventId,
            locationId,
            0,
            sendMessage,
            notes,
            employeeId
          )
          .then(() => {
            onComplete("Cover-Request Created");
          });
        break;

      case EventEditEnum.CreateShiftRequest:
        reqApi
          .makeShiftRequest(
            user,
            scheduleEventId,
            locationId,
            0,
            sendMessage,
            notes
          )
          .then((results) => {
            if (results.error) {
              dlg.showAlert("Shift-Request Failed", results.message, () => {});
            } else {
              onComplete(results.message); // "Shift-Request Created"
            }
          });
        break;

      case EventEditEnum.DeleteShiftRequest:
        reqApi
          .removeShiftRequest(
            user,
            locationId,
            scheduleEventId,
            eventType,
            eventStatus,
            undefined
          )
          .then(() => {
            onComplete("Shift-Request Removed");
          });
        break;

      case EventEditEnum.UndoCoverRequest:
        reqApi.undoCoverRequest(user, scheduleEventId).then(() => {
          onComplete("Removed Cover-Request");
        });
        break;

      case EventEditEnum.Delete:
        reqApi
          .deleteEvent(
            user,
            locationId,
            scheduleEventId,
            eventType,
            eventStatus,
            undefined
          )
          .then(() => {
            onComplete("Deleted");
          });
        break;
    }
  };

  if (
    !eventEditAuth ||
    !auth ||
    !auth.locations ||
    auth.locations.length === 0 ||
    eventEditAuth.usage === EventEditEnum.None ||
    scheduleEventId <= 0
  ) {
    return <div />;
  }

  return (
    <>
      <IonButton
        className="apply-button"
        expand="block"
        onClick={() => {
          setShowRequestModal(true);
        }}
      >
        {eventEditAuth.buttonText}
      </IonButton>

      <IonModal
        isOpen={showRequestModal}
        cssClass="mini-modal event-request-modal"
        onDidDismiss={() => setShowRequestModal(false)}
      >
        <EventRequestModal
          onCancel={() => {
            setShowRequestModal(false);
          }}
          onOk={onApply}
          auth={auth}
          eventEditAuth={eventEditAuth}
          scheduleEventId={scheduleEventId}
          locSettings={authHelper.getLocSettingsForLocationId(auth, locationId)}
        />
        {dlg.alertBoxJsx}
      </IonModal>
    </>
  );
};

export default EventRequestEditor;
