// ------------------------------------------------------------------------------------------------
export function initialize() {
  console.log("firebase initialized");
  firebase.initializeApp(firebaseConfig);
}

// ------------------------------------------------------------------------------------------------
export function log(eventName: string, eventParams?: any) {
  firebase.analytics().logEvent(eventName, eventParams);
}

// ------------------------------------------------------------------------------------------------
export function setFirebaseUserProperties(user: ApplicationUser, auth: AuthorizedItems) {
  if (user.isLoggedin) {
    const environment = env.settings.env.item(user.environment);
    if (user.username !== undefined) {
      firebase.analytics().setUserId(user.username);
    }

    firebase.analytics().setUserProperties({
      userRole: auth.role,
      subscriptionType: getSubscriptionTypeName(auth.subscriptionType),
      businessName: auth.businessName,
      userName: user.username,
      version: env.settings.appVersionString,
      environment: environment.name
    });
  }
}
